import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { MessageService } from "primeng/components/common/messageservice";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Router } from '@angular/router';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'app-dynamic-list',
  templateUrl: './dynamic-list.component.html',
  styleUrls: ['./dynamic-list.component.scss'],
  providers: []
})
export class DynamicListComponent implements OnInit {
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();
  

  data:any = [];  // 主页策略列表

  loadingShow: any = { flag: false }; // loading框

  strategyName:string ; // 策略名称

  shareUserName:string ; // 分享人

  totalNum:any; // 列表总条数
  pageNum:number = 1// 当前页码
  pageSize:number = 10// 页面显示几条
  first:number = 0;
  tableMesg: any = tableMessage;
  personPage:boolean = false;


  
    params: any = {
      businessId: 'C0001',
    };

  tabTable:any = []; 

  dialogDisplay:boolean = false;  // 分享策略列表

  constructor(
    private messageService: MessageService,
    private httpService: CustomSearchHttpService,
    private commonFunc: Customtools,
    public router: Router,
    public commfunc: Commfunc,
  ) {

  }
  ngOnInit() {
    this.queryData(true);
  }

  // 查询按钮
  query(){
    // debugger
    this.pageNum = 1;
    this.pageSize = 10;
    this.first = 0;
    this.queryData(true);
      }
    // 分页
    paginate(event) {
      // 每页显示的条数;
      this.pageSize = event.rows;
      // 当前页
      this.pageNum = event.page + 1;
      this.first = event.first;
      // 调用查询的方法
      this.queryData(true);
    }
  
  // 分享动态查询
  queryData(isMessage?) {
    const params = {
      businessId: 'C0001',
      strategyName:this.strategyName,
      shareUserName:this.shareUserName
    }
    params['pageNum'] = this.pageNum,// 当前页码
    params['pageSize'] = this.pageSize// 页面显示几条
    const configParam = {
      httpService: this.httpService,
      loadingFlag: false,
      messageFlag: !isMessage,
      inputParam: params,
      message: this.messageService,
      injectName: 'strategyInfoShareDynamicQuery',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
          this.tabTable = bodyData.dataList;
          this.totalNum = bodyData.totalNum;
      }
    });
  }

  // 展示分享策略的列表
  showList(){
    this.dialogDisplay = true;
  }

  // 返回按钮
  back(){
    // this.dialogDisplay = false;
    this.outValueEmit.emit(false);
  }

  
  // 重置按钮
  reset(){
    this.strategyName = '';
    this.shareUserName = '';
    this.query();
  }

  selectOwner() {
    this.personPage = true;
}
  // 选择创建人之后的回调函数
  workerCall(event) {
    this.personPage = false;
    if (event.length > 0) {
        this.shareUserName = event[0].tellerName;
    }

}

    // 跳转到搜索结果页面
    toSearchResult(data){
      const strategyId= this.commfunc.compileStr(data.strategyId);
      this.router.navigate(['/pages/tzb/custom/custom-search/search-result-pages',], { 
        queryParams:{ strategyId: strategyId,strategyFlag : '1'}
      })
    
    }

}