import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { MessageService } from "primeng/components/common/messageservice";
import { Message } from "primeng/components/common/message";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
import { ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { UisftechCommonHttpService } from 'app/@uisftech/common/http/uisftech-common-http.service';
import { PartyManageBean } from "app/pages/tzb/common/base-manage/party-manage/bean/party-bean";

@Component({
    selector: 'app-select-assist-store-organization',
    templateUrl: './select-assist-store-organization.component.html',
    styleUrls: ['./select-assist-store-organization.component.scss'],
    providers: []
})
export class SelectAssistStoreOrganizaitionComponent implements OnInit {
    @Input() inValueItem: any;
    @Output() outValueEmit: EventEmitter<any> = new EventEmitter()

    creat_user: any;
    // 表格中显示的信息
    tabMesg: any = tableMessage;
    precredit: string = '机构管理';
    code: any;//码值
    //机构类型
    orgType: any[] = [];
    //机构状态
    orgStatus: any[] = [];
    //机构级别
    operatingLevel: any[] = [];
    //法人标识
    legalFlag: any[] = []
    // 表体内容
    tableData: any;
    partyManageBean: PartyManageBean = new PartyManageBean();
    //机构类型
    partyType: any = [];
    //机构状态 
    partyStatus: any = [];
    // 机构等级
    partyLevel: any = [];


    //总页数
    total: string;
    //每页多少条
    pageSize: string = '10';

    //提示信息
    msgs: Message[] = [];

    //模态框的标题
    headerTitle: string = "机构同步";
    //模态框是否显示
    display: boolean = false;
    //模态框
    showModel: string;
    //传出的值
    inValue: string;
    //接收后台的数据
    list: any[]
    list1: any[]
    currentPage: any;
    //
    setOrgLValue: any
    first: number = 0;
    selectedCar: any = [];
    tempArr: any = [];

    loadingShow: any = { flag: false }; // loading框

    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public uisftechCommonHttpService: UisftechCommonHttpService,
        private messageService: MessageService,
        private commonFunc: Customtools,
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.orgType = this.code['OrgTypes'];
        this.orgStatus = this.code['OrgStatus'];
        this.operatingLevel = this.code['OperatingLevel'];
        this.legalFlag = this.code['LegalFlag']
    }
    ngOnInit() {
        this.partyManageBean.rowNum = '10';
        this.partyManageBean.startRow = '1';
        this.queryClick();

        let _thiss = this;
        document.onkeydown = function (event) {
            if (event && event.keyCode === 13) {
                _thiss.query();
            }
        }
        this.select();
    }
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    select() {
        if (this.inValueItem && this.inValueItem != '') {
            this.tempArr = this.inValueItem.split(',')
            this.tempArr.forEach(el => {
                const params = {
                    orgId: el,
                    pageSize: 10,
                    pageNum: 1
                }
                const configParam = {
                    httpService: this.commonHttpService,
                    loadingFlag: false,
                    messageFlag: false,
                    inputParam: params,
                    message: this.messageService,
                    injectName: 'queryOrgByCombConditionsLevel',
                    loadingShow: this.loadingShow,
                    messageInfo: '查询成功',
                }
                this.commonFunc.haddleData(configParam, (bodyData) => {
                    if (bodyData) {
                        this.selectedCar.push(bodyData.orgInfoList[0])
                    }
                });
            });
        }

    }

    //查询
    queryClick() {
        this.creat_user = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.partyManageBean.flag = '0';
        this.commonHttpService.queryOrgByCombConditionsLevel(this.partyManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.orgInfoList;
                this.total = data.rowCount;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    query() {//点击查询
        this.partyManageBean.flag = '0';
        this.first = 0;
        this.partyManageBean.startRow = "1";
        this.commonHttpService.queryOrgByCombConditionsLevel(this.partyManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.orgInfoList;
                this.total = data.rowCount;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }


    //分页
    paginate(event) {

        //每页显示的条数
        this.partyManageBean.rowNum = event.rows;
        //当前页
        this.currentPage = event.page + 1 + '';
        this.partyManageBean.startRow = this.currentPage;
        this.first = event.page * event.rows;
        // this.first=event.page;
        //调用查询的方法
        this.queryClick();
    }

    // 重置
    reset() {
        this.partyManageBean.orgName = '',
            this.partyManageBean.orgId = '',
            this.partyManageBean.superiorManageOrg = '';
        this.partyManageBean.orgType = '';
        this.partyManageBean.orgStatus = '';
        this.partyManageBean.operatingLevel = '';

    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

    submit() {
        // console.log(this.selectedCar2);
        this.outValueEmit.emit(this.selectedCar);
    }

    back() {
        // this.outValueEmit.emit(false);
        this.selectedCar = [];
    }
}