import { OnInit, Component, Output, EventEmitter, Input, OnChanges } from "@angular/core";

@Component({
    selector: 'share-strategy',
    templateUrl: './share-strategy.component.html',
    styleUrls: ['./share-strategy.component.scss'],
    providers: []
})

export class ShareStrategyComponent implements OnInit,OnChanges {

    @Input() inValue: any;
    @Input() shareType: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    condition: string;//条件
    shareFlag: boolean = false;//

    constructor(

    ) {

    }

    ngOnInit() {
        
    }

    ngOnChanges() {
        if(this.shareType && this.shareType != ''){
            if(this.shareType == 'add'){
                this.add();
            }else if(this.shareType == 'share'){
                this.update();
            }
        }
    }

    //
    add(){
        this.shareFlag = true;
    }

    //
    update(){
        this.shareFlag = false;
    }

    //确认
    confirm() {
        this.outValue.emit({ label: this.condition,flag: true });
    }

    //取消
    cancel() {
        this.condition = '';
        this.outValue.emit({ flag: false });
    }
}