import { Component, OnInit } from '@angular/core';
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { Message } from "primeng/components/common/message"; 
import { ActivatedRoute } from "@angular/router";
@Component({
  selector: 'app-everyone-config',
  templateUrl: './everyone-config.component.html',
  styleUrls: ['./everyone-config.component.scss'],
  providers: []
})
export class EveryoneConfigComponent implements OnInit {

  data:any = [
    {isNew:'',isTopSearch:''},
    {isNew:'',isTopSearch:''},
    {isNew:'',isTopSearch:''},
    {isNew:'',isTopSearch:''},
    {isNew:'',isTopSearch:''},
    {isNew:'',isTopSearch:''},
  ]; 
  
  dialogDisplay:boolean = false;

  index:any;

  msgs: Message[] = [];

  lenovoData: any = [ [], [] , [] ,[] , [] , [] ];  // 联想出来的数据
  
  searchContent: string; // 用户输入框中输入的内容

  params:any;

  loadingShow: any = { flag: false }; // loading框

  queryParams:any;

  inValueItem:any;

  constructor(
    private httpService: CustomSearchHttpService,
    private messageService: MessageService,
    private commonFunc: Customtools,
    private confirmationService: ConfirmationService,
    public router: Router,
    private activeRoute: ActivatedRoute,
  ) {

  }
  ngOnInit() {
    this.activeRoute.queryParams.subscribe(params => {
      this.queryParams = JSON.parse(params.data);
    })
    this.queryParams.forEach((el,index) => {
      this.data[index] = el;
    });
  }

    // 切换radiobutton
    selsectRadio(item,i){
      if(item === 'isNew'){
        this.data[i].isNew = '1';
        this.data[i].isTopSearch = '0';
      }else if(item === 'isTopSearch'){
        this.data[i].isNew = '0';
        this.data[i].isTopSearch = '1';
      }
    }



  // 保存配置
  save(){
    console.log(this.data);
    const params = {
      businessId:'C0001',
      recommendType:'01',
      dataList:[]
    }
    this.data.forEach((el,index) => {
      if(el.seqId){
        if(el.conditionId){
          params.dataList.push(el)
        }else{
          params.dataList.push({
            seqId:index + 1 + '',
            conditionName : '',
            conditionId:'',
            isNew:'',
            isTopSearch:''
          })
        }
      }
    });
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: true,
      inputParam: params,
      message: this.messageService,
      injectName: 'conditionRecommendSave',
      loadingShow: this.loadingShow,
      messageInfo: '保存成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.router.navigate(['pages/tzb/custom/custom-search']);
      }
    });
  }

  back(){
    this.router.navigate(['pages/tzb/custom/custom-search']);
  }

  click(i){
    this.dialogDisplay = true;
    this.index = i; 
  }

  // 选择之后的回调函数
  callMoreTarget(event){
    this.dialogDisplay = false;
    if(event){
      this.data[this.index].conditionName = event.indicatorName;
      this.data[this.index].conditionId = event.indicatorId;
      this.data[this.index].seqId = this.index + 1+"";
    }

  }

  // 清空
  delete(){
    this.data = [
      {seqId:'1',isNew:'',isTopSearch:''},
      {seqId:'2',isNew:'',isTopSearch:''},
      {seqId:'3',isNew:'',isTopSearch:''},
      {seqId:'4',isNew:'',isTopSearch:''},
      {seqId:'5',isNew:'',isTopSearch:''},
      {seqId:'6',isNew:'',isTopSearch:''},
    ]; 
  }


}