import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { MessageService } from "primeng/components/common/messageservice";
import { Message } from "primeng/components/common/message";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";

@Component({
    selector: 'app-select-system-label',
    templateUrl: './select-system-label.component.html',
    styleUrls: ['./select-system-label.component.scss'],
    providers: []
})
export class SelectSystemLabelComponent implements OnInit {
    @Input() inValueItem: any;
    @Output() outValueEmit: EventEmitter<any> = new EventEmitter();

    selectSystemLabelGroupForm: FormGroup;  // 查询表单

    tabTable: any = []    // 查询结果

    selectedCar: any = [];  // 列表选中的数据集合

    sendData: any = []; // 要发送给查询页面的数据集合


    loadingShow: any = { flag: false }; // loading框

    pageSize: number = 10;//每页条数
    pageNum: number = 1;//当前页数
    totalNum: number;
    first: number = 0;
    code: any;
    createOrgId: string;// 机构ID
    creatorId: string;// 创建人ID
    orgDisplay: boolean = false; // 机构模态框
    personPage: boolean = false; // 员工模态框
    tempArr: any = [];
    tableMesg: any = tableMessage;





    constructor(
        private messageService: MessageService,
        private httpService: CustomSearchHttpService,
        private commonFunc: Customtools,
        private commfunc: Commfunc,
        private fb: FormBuilder,
    ) {
    }
    ngOnInit() {
        this.selectSystemLabelGroupForm = this.fb.group({
            'labelName': [''],// 标签名称
            'labelNum': [''], // 标签编号
        });
        this.query();
        this.select();
    }


    select() {
        if (this.inValueItem && this.inValueItem != '') {
            this.tempArr = this.inValueItem.split(',')
            this.tempArr.forEach(el => {
                const params = {
                    labelNum: el,
                    pageSize: 10,
                    pageNum: 1,
                    labelType: '1',  // labelType 为1  查询系统标签
                    status: '1',
                }
                const configParam = {
                    httpService: this.httpService,
                    loadingFlag: false,
                    messageFlag: false,
                    inputParam: params,
                    message: this.messageService,
                    injectName: 'selectLabelInfoNew',
                    loadingShow: this.loadingShow,
                    messageInfo: '查询成功',
                }
                this.commonFunc.haddleData(configParam, (bodyData) => {
                    if (bodyData) {
                        this.selectedCar.push(bodyData.labelTypeList[0])
                    }
                });
            });
        }

    }

    // 点击查询
    doquery() {
        this.first = 0;
        this.pageNum = 1;
        this.query();
    }
    // 重置
    reset() {
        this.selectSystemLabelGroupForm.reset();
    }

    // 分页
    paginate(event) {
        // 每页显示的条数;
        this.pageSize = event.rows;
        // 当前页
        this.pageNum = event.page + 1;
        this.first = event.first;
        // 调用查询的方法
        this.query();
    }

    // 查询系统标签
    query() {
        const params = {
            labelType: '1',  // labelType 为1  查询系统标签
            status: '1',
            pageSize: this.pageSize,
            pageNum: this.pageNum
        }
        for (var key in this.selectSystemLabelGroupForm.value) {
            if (this.selectSystemLabelGroupForm.value[key]) {
                params[key] = this.selectSystemLabelGroupForm.value[key]
            }
        }
        const configParam = {
            httpService: this.httpService,
            loadingFlag: true,
            messageFlag: true,
            inputParam: params,
            message: this.messageService,
            injectName: 'selectLabelInfoNew',
            loadingShow: this.loadingShow,
            messageInfo: '查询成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                console.log(bodyData)
                this.tabTable = bodyData.labelTypeList;
                this.totalNum = bodyData.totalNum;
            }
        });
    }

    // 清空
    back() {
        this.selectedCar = [];
        this.sendData = [];
    }

    // 确认
    submit() {
        this.selectedCar.forEach(el => {
            if (el.labelName && el.labelNum) {
                // debugger
                this.sendData.push({
                    labelName: el.labelName,
                    labelNum: el.labelNum
                })
            }
        });
        this.outValueEmit.emit(this.sendData);
    }





}