import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { MessageService } from "primeng/components/common/messageservice";
import { Message } from "primeng/components/common/message";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { ConfirmationService } from "primeng/primeng";
import { Router, ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-add-free-group',
    templateUrl: './add-free-group.component.html',
    styleUrls: ['./add-free-group.component.scss'],
    providers: []
})

export class AddFreeGroupComponent implements OnInit {

    condition: string;//条件
    @Input() customerList: any;
    @Output() outValueEmit: EventEmitter<any> = new EventEmitter();
    selectOptionalCustomGroupForm: FormGroup;  // 查询表单

    tabTable: any = []    // 查询结果

    selectedCar: any = [];  // 列表选中的数据集合

    sendData: any = []; // 要发送给查询页面的数据集合


    loadingShow: any = { flag: false }; // loading框

    groupStatus: any = []//群状态下拉框
    msgs: Message[] = [];

    pageSize: number = 10;//每页条数
    pageNum: number = 1;//当前页数
    totalNum: number;
    first: number = 0;
    code: any;
    createOrgId: string;// 机构ID
    creatorId: string;// 创建人ID
    orgDisplay: boolean = false; // 机构模态框
    personPage: boolean = false; // 员工模态框
    tableMesg: any = tableMessage;
    customNo: string;    // 客户号指标ID
    customName: string;  // 客户名称指标ID
    outputFieldList: any = [];    // 客户号和客户名称的指标ID集合
    paramsList: any = [];  // 客户集合
    toGroupDisplay: boolean = false; // 确定跳转群视图弹窗；
    custName: string;
    commonHeader: any;


    constructor(
        private messageService: MessageService,
        private httpService: CusOperationHttpService,
        private commonFunc: Customtools,
        private commfunc: Commfunc,
        private fb: FormBuilder,
        private httpService_: CustomSearchHttpService,
        public router: Router,
        private confirmationService: ConfirmationService,
    ) {
        this.codeValues();
        this.groupStatus = this.code['GroupStatus']
    }
    ngOnInit() {
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.selectOptionalCustomGroupForm = this.fb.group({
            'freeGroupId': [''],//群号
            'freeGroupName': [''],//群名称
            // 'creatorId': ['commonHeader.userId'],//分组所有者ID
        });
        this.query();
        this.queryOutList('客户号');
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    click(item) {
        console.log(item);
    }
    // 点击查询
    doquery() {
        this.first = 0;
        this.pageNum = 1;
        this.query();
    }
    // 重置
    reset() {
        this.selectOptionalCustomGroupForm.reset();
    }

    // 分页
    paginate(event) {
        // 每页显示的条数;
        this.pageSize = event.rows;
        // 当前页
        this.pageNum = event.page + 1;
        this.first = event.first;
        // 调用查询的方法
        this.query();
    }

    // 查询自选客户群
    query() {
        // creatorId
        const params = {
            pageSize: this.pageSize,
            pageNum: this.pageNum
        }
        for (var key in this.selectOptionalCustomGroupForm.value) {
            if (this.selectOptionalCustomGroupForm.value[key]) {
                params[key] = this.selectOptionalCustomGroupForm.value[key]
            }
        }
        params['creatorId'] = this.commonHeader.userId;
        const configParam = {
            httpService: this.httpService,
            loadingFlag: false,
            messageFlag: false,
            inputParam: params,
            message: this.messageService,
            injectName: 'selectFreeGroup',
            loadingShow: this.loadingShow,
            messageInfo: '查询成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                console.log(bodyData)
                this.tabTable = bodyData.freeGroupList;
                this.totalNum = bodyData.totalNum;
            }
        });
    }
    selectOrg() {
        // 选择机构
        this.orgDisplay = true;
    }

    selectOwner() {
        this.personPage = true;
    }

    // 选择机构之后的回调函数
    returnOrg(event) {
        if (event.hasOwnProperty('orgName')) {
            this.selectOptionalCustomGroupForm.controls['createOrgId'].setValue(event.orgName);
            this.createOrgId = event.orgId;
        }
        this.orgDisplay = false;
    }

    // 选择创建人之后的回调函数
    workerCall(event) {
        this.personPage = false;
        if (event.length > 0) {
            this.selectOptionalCustomGroupForm.controls['creatorId'].setValue(event[0].tellerName);
            this.creatorId = event[0].tellerId;
        }

    }

    // 返回
    back() {
        this.outValueEmit.emit(false);
    }

    // 确认
    submit() {
        if (this.selectedCar.length == 0) {
            this.messageService.add({ severity: 'error', summary: '提示', detail: '请选择要加入的客户群' });
        } else {

            let tempCustNo = this.outputFieldList[0];
            let tempCustName = this.outputFieldList[1];
            this.customerList.forEach((el, index) => {
                this.paramsList.push({
                    custNo: el[tempCustNo],
                    custName: el[tempCustName]
                })
            });
            this.customerList.forEach((el, index) => {
                if (index === 0) {
                    this.custName = el[tempCustName];
                }
            });
            const params = {
                freeGroupId: this.selectedCar.freeGroupId,
                custInfoList: this.paramsList
            }
            const configParam = {
                httpService: this.httpService,
                loadingFlag: true,
                messageFlag: true,
                inputParam: params,
                message: this.messageService,
                injectName: 'addFreeGroupCustList',
                loadingShow: this.loadingShow,
                messageInfo: '加入成功',
            }
            this.commonFunc.haddleData(configParam, (bodyData) => {
                if (bodyData) {
                    // this.toGroupDisplay = true;
                    this.outValueEmit.emit({
                        custName:this.custName,
                        length:this.customerList.length,
                        groupName:this.selectedCar.freeGroupName,
                        freeGroupId:this.selectedCar.freeGroupId,
                    });
                    // this.confirm();
                    // this.outValueEmit.emit(false);
                }
            });
        }

    }


    // 查询客户号和客户名称的指标ID
    queryOutList(name) {
        const params = {
            inputParams: { sceneType: '002', status: '1', indicatorName: name }
        };
        const configParam = {
            httpService: this.httpService_,
            loadingFlag: true,
            messageFlag: false,
            inputParam: params,
            message: this.messageService,
            injectName: 'queryIndicatorApplScene',
            loadingShow: this.loadingShow,
            messageInfo: '查询成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                console.log(bodyData)
                // this.customNo = bodyData.outputParams.indicatorList[0].indicatorId;
                this.outputFieldList.push(bodyData.outputParams.indicatorList[0].indicatorId); // 查询列表 表头的ID（客户号和客户名称指标ID）
                if (this.outputFieldList.length < 2) {
                    this.queryOutList('客户名称');
                } else {
                    // this.queryData();
                }
            }
        });
    }



    onHide(){
        this.toGroupDisplay = false;
        
    }

}