import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { MessageService } from "primeng/components/common/messageservice";
import { Message } from "primeng/components/common/message";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
    selector: 'app-select-user-label',
    templateUrl: './select-user-label.component.html',
    styleUrls: ['./select-user-label.component.scss'],
    providers: []
})
export class SelectUserLabelComponent implements OnInit {
    @Input() inValueItem: any;
    @Output() outValueEmit: EventEmitter<any> = new EventEmitter();

    selectUserLabelForm: FormGroup;  // 查询表单

    tabTable: any = []    // 查询结果

    selectedCar: any = [];  // 列表选中的数据集合

    sendData: any = []; // 要发送给查询页面的数据集合

    loadingShow: any = { flag: false }; // loading框

    pageSize: number = 10;//每页条数
    pageNum: number = 1;//当前页数
    totalNum: number;
    first: number = 0;
    tableMesg: any = tableMessage;
    orgDisplay: boolean = false; // 机构模态框
    personPage: boolean = false; // 员工模态框
    labelAttr: any = [
        { label: '全部', value: '2' },
        { label: '独有', value: '0' },
        { label: '共享', value: '1' },
    ];//标签属性
    code: any;
    chName: any;
    tempArr: any = [];


    constructor(
        private messageService: MessageService,
        private httpService: CustomSearchHttpService,
        private commonFunc: Customtools,
        private commfunc: Commfunc,
        private fb: FormBuilder,
    ) {
        // this.codeVal();
        // this.labelAttr = this.code['LabelAttr'];
    }
    ngOnInit() {
        this.chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.selectUserLabelForm = this.fb.group({
            'labelName': [''],// 标签名称
            'labelFlag': [''],// 标签属性
            'creatorName': [''],// 创建人
            'orgName': [''],// 创建机构
            'orgId': [''], // 创建机构
        });
        this.selectUserLabelForm.controls['creatorName'].setValue(this.chName.userName);
        this.query();
        this.select();
    }


    select() {
        if (this.inValueItem && this.inValueItem != '') {
            this.tempArr = this.inValueItem.split(',')
            this.tempArr.forEach(el => {
                const params = {
                    labelNum: el,
                    pageSize: 10,
                    pageNum: 1,
                    labelType: '2',
                    status: '1',
                }
                const configParam = {
                    httpService: this.httpService,
                    loadingFlag: false,
                    messageFlag: false,
                    inputParam: params,
                    message: this.messageService,
                    injectName: 'selectLabelInfoNew',
                    loadingShow: this.loadingShow,
                    messageInfo: '查询成功',
                }
                this.commonFunc.haddleData(configParam, (bodyData) => {
                    if (bodyData) {
                        this.selectedCar.push(bodyData.labelTypeList[0])
                    }
                });
            });
        }

    }

    // 点击查询
    doquery() {
        this.first = 0;
        this.pageNum = 1;
        this.query();
    }
    // 重置
    reset() {
        this.selectUserLabelForm.reset();
    }

    // 分页
    paginate(event) {
        // 每页显示的条数;
        this.pageSize = event.rows;
        // 当前页
        this.pageNum = event.page + 1;
        this.first = event.first;
        // 调用查询的方法
        this.query();
    }

    // 查询自选客户群
    query() {
        const params = {
            labelType: '2',
            status: '1',
            pageSize: this.pageSize,
            pageNum: this.pageNum
        }
        for (var key in this.selectUserLabelForm.value) {
            if (this.selectUserLabelForm.value[key]) {
                params[key] = this.selectUserLabelForm.value[key]
            }
        }
        if (!this.selectUserLabelForm.value['labelFlag']|| this.selectUserLabelForm.value['labelFlag'] == '2') {
            params['shareFlag'] = '1';
            params['labelAttribute'] = ''
        } else {
            params['shareFlag'] = this.selectUserLabelForm.value['labelFlag'];
            params['labelAttribute'] = this.selectUserLabelForm.value['labelFlag']
        }

        const configParam = {
            httpService: this.httpService,
            loadingFlag: false,
            messageFlag: false,
            inputParam: params,
            message: this.messageService,
            injectName: 'selectLabelInfoNew',
            loadingShow: this.loadingShow,
            messageInfo: '查询成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                console.log(bodyData)
                this.tabTable = bodyData.labelTypeList;
                this.totalNum = bodyData.totalNum;
            }
        });
    }

    // 清空
    back() {
        this.selectedCar = [];
        this.sendData = [];
    }

    // 确认
    submit() {
        this.selectedCar.forEach(el => {
            if (el.labelName && el.labelNum) {
                this.sendData.push({
                    labelName: el.labelName,
                    labelNum: el.labelNum
                })
            }
        });
        this.outValueEmit.emit(this.sendData);
    }

    selectOrg() {
        // 选择机构
        this.orgDisplay = true;
    }

    selectOwner() {
        this.personPage = true;
    }

    // 选择机构之后的回调函数
    returnOrg(event) {
        if (event.hasOwnProperty('orgName')) {
            this.selectUserLabelForm.controls['orgName'].setValue(event.orgName);
            this.selectUserLabelForm.controls['orgId'].setValue(event.orgId);
        }
        this.orgDisplay = false;
    }

    // 选择创建人之后的回调函数
    workerCall(event) {
        this.personPage = false;
        if (event.length > 0) {
            this.selectUserLabelForm.controls['creatorName'].setValue(event[0].tellerName);
        }

    }



}