import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { MessageService } from "primeng/components/common/messageservice";
import { Message } from "primeng/components/common/message";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { UisftechCommonHttpService } from 'app/@uisftech/common/http/uisftech-common-http.service';
import { ConfirmationService } from 'primeng/primeng';
import { QueryUserBean } from "app/@uisftech/common/component/queryUser/bean/queryUser.bean";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'app-select-main-cantact',
  templateUrl: './select-main-cantact.component.html',
  styleUrls: ['./select-main-cantact.component.scss'],
  providers: [UisftechCommonHttpService, ConfirmationService]
})
export class SelectMainCantactComponent implements OnInit {
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();
  @Input("in-value") inValue: string;
  @Input() inValueItem: any;
  cities: any;

  precredit: string = '用户管理';
  // 表体内容
  tableData: any;
  tableData1: any
  queryUserBean: QueryUserBean = new QueryUserBean()
  //总页数
  /**
   * 2018.1.15--
   */
  total: any = 1;
  //每页多少条
  pageSize: any = 10;
  first: any = 0;
  //表格中无数据时显示的内容
  tabMesg: any = tableMessage
  /**
   * --2018.1.15
   */
  //提示信息
  msgs: Message[] = [];
  // 所属机构
  status: any = [];
  // 员工状态
  opera: any = [];

  //返回的数据
  personArr: any = [];
  //模态框的标题
  headerTitle: string = "柜员同步";
  //模态框是否显示
  display: boolean = false;
  //模态框
  showModel: string;

  selectedCities: any;
  userform: FormGroup; //校验  
  //session
  session_info: any;
  selectVal: any;
  //接收机构名称
  list: any[];
  selectedCar2: any = [];
  user1: any;
  userId: string;
  dispalyWork: boolean = true;
  tempArr: any = [];
  loadingShow: any = { flag: false }; // loading框

  constructor(
    public uisftechCommonHttpService: UisftechCommonHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    private messageService: MessageService,
    private commonFunc: Customtools,
  ) {
    this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (this.user1.userId) {
      this.userId = this.user1.userId;
    }
  }

  ngOnInit() {
    this.session_info = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.inValue) {
      if (this.inValue == 'custManager') {
        this.queryUserBean.tellerId = this.userId;
        this.query2();
        return
      } else {
        this.queryUserBean.orgId = this.inValue;
      }
    } else {
      this.queryUserBean.orgId = this.session_info.orgId;
    }
    this.queryUserBean.pageSize = '10';
    this.queryUserBean.pageNum = '1';
    this.queryClick();
    this.select();
  }

  ngOnChanges() {

  }

  select() {
    if (this.inValueItem && this.inValueItem != '') {
      this.tempArr = this.inValueItem.split(',')
      this.tempArr.forEach(el => {
        const params = {
          tellerId: el,
          pageSize: 10,
          pageNum: 1,
        }
        const configParam = {
          httpService: this.uisftechCommonHttpService,
          loadingFlag: false,
          messageFlag: false,
          inputParam: params,
          message: this.messageService,
          injectName: 'queryTellerByCombConditions',
          loadingShow: this.loadingShow,
          messageInfo: '查询成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
          if (bodyData) {
            this.selectedCar2.push(bodyData.tellerList[0])
          }
        });
      });
    }
  }

  //点击查询
  query() {
    this.first = 0;
    this.queryUserBean.pageSize = '10';
    this.queryUserBean.pageNum = '1';
    this.uisftechCommonHttpService.queryTellerByCombConditions(this.queryUserBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.tellerList;
        let tmpTotal = data.total;
        this.total = tmpTotal * 1;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
    //   }

  }

  //查询
  queryClick() {
    this.first = 0;

    this.uisftechCommonHttpService.queryTellerByCombConditions(this.queryUserBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.tellerList;
        /**
         * 2018.1.15--
         */
        let tmpTotal = data.total;
        this.total = tmpTotal * 1;
        /**
         * --2018.1.15
         */
        // this.msgs = [];
        // this.msgs.push({ severity: "success", summary: '提示', detail: data.returnCode.message })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  // 重置
  reset() {
    this.queryUserBean = new QueryUserBean();
  }

  //点击查询
  query2() {
    this.first = 0;
    this.queryUserBean.pageSize = '10';
    this.queryUserBean.pageNum = '1';
    this.queryUserBean.flag = '1';
    this.dispalyWork = false;
    this.uisftechCommonHttpService.queryTellerByCombConditions(this.queryUserBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.tellerList;
        let tmpTotal = data.total;
        this.total = tmpTotal * 1;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

  }
  //分页
  paginate(event) {
    /**
     * 2018.1.15--
     */
    this.queryUserBean.pageSize = event.rows * 1 + '';
    //当前页
    let currentPage = event.page + 1 + '';
    this.queryUserBean.pageNum = currentPage;
    this.first = event.page * event.rows;
    /**
     * --2018.1.15
     */
    //调用查询的方法
    this.queryClick();
  }
  // onRowSelect(event){
  //   this.personArr=[];
  //   this.personArr.push(event.data)
  //   this.outValue.emit(this.personArr);
  // }

  submit() {
    console.log(this.selectedCar2);
    this.outValueEmit.emit(this.selectedCar2);
  }

      // 清空
      back() {
        this.selectedCar2 = [];
    }

}