import { Component, OnInit } from '@angular/core';
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Message } from "primeng/components/common/message";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
@Component({
  selector: 'app-my-strategy',
  templateUrl: './my-strategy.component.html',
  styleUrls: ['./my-strategy.component.scss'],
  providers: []
})
export class MyStrategyComponent implements OnInit {

  tableData: any = []; // 返回数据
  pageSize = 20; // 页面显示几条
  pageNum = 1; // 当前页码
  totalNum: any;  // 总条数
  first: number = 0;
  dialogDisplay: boolean = false; // 子组件模态框display
  inValue = {}; // 控制子组件的数据
  inValueItem; // 导入子组件的数据
  modalObj = { // 控制子组件显示和数据传递
    'update': {
      updateDisplay: true,
      modalName: '编辑策略'
    },
    'share': {
      shareDisplay: true,
      modalName: '分享策略'
    }
  }

  loadingShow: any = { flag: false }; // loading框
  tableMesg: any = tableMessage;
  msgs: Message[] = [];


  LogicDesc: any = [
    { label: '且', value: '01' },
    { label: '或', value: '02' },
    { label: '自定义', value: '03' }
  ]

  sort1: boolean = true;
  sort2: boolean = true;
  upFlag1: boolean = false;
  downFlag1: boolean = false;
  upFlag2: boolean = false;
  downFlag2: boolean = false;
  sortField: string = '-createdStamp';  // 排序字段


  constructor(
    private httpService: CustomSearchHttpService,
    private messageService: MessageService,
    private commonFunc: Customtools,
    private confirmationService: ConfirmationService,
    public router: Router,
    public commfunc: Commfunc,
  ) {

  }
  ngOnInit() {
    this.query();
    // this.tableData.push({
    //   strategyName: '策略名称',
    //   logicType:'01,02'
    // })
  }


  // 排序
  sortClick(i) {
    // switch (i) {
    //   case '1':
    //     this.upFlag1 = false;
    //     this.downFlag1 = true;
    //     // this.upFlag2 = false;
    //     // this.downFlag2 = false;
    //     this.sortField = '-createdStamp'
    //     break;
    //   case 'downFlag1':
    //     this.upFlag1 = true;
    //     this.downFlag1 = false;
    //     // this.upFlag2 = false;
    //     // this.downFlag2 = false;
    //     this.sortField = '+createdStamp'
    //     break;
    //   case 'upFlag2':
    //     this.upFlag2 = false;
    //     this.downFlag2 = true;
    //     // this.upFlag1 = false;
    //     // this.downFlag1 = false;
    //     this.sortField = '-lastUpdatedStamp'
    //     break;
    //   case 'downFlag2':
    //     this.upFlag1 = true;
    //     this.downFlag1 = false;
    //     // this.upFlag1 = false;
    //     // this.downFlag1 = false;
    //     this.sortField = '+lastUpdatedStamp'
    //     break;

    //   default:
    //     break;
    // }
    if (i === 1) {
      this.sort1 = false;
      this.sort2 = true;
      this.upFlag2 = false;
      this.downFlag2 = false;
      if (this.upFlag1) {
        this.upFlag1 = false;
        this.downFlag1 = true;
        this.sortField = '-createdStamp';
      } else if (this.downFlag1) {
        this.upFlag1 = true;
        this.downFlag1 = false;
        this.sortField = '+createdStamp';
      } else {
        this.upFlag1 = true;
        this.downFlag1 = false;
        this.sortField = '+createdStamp';
      }
    } else if (i === 2) {
      this.sort1 = true;
      this.sort2 = false;
      this.upFlag1 = false;
      this.downFlag1 = false;
      if (this.upFlag2) {
        this.upFlag2 = false;
        this.downFlag2 = true;
        this.sortField = '-lastUpdatedStamp';
      } else if (this.downFlag2) {
        this.upFlag2 = true;
        this.downFlag2 = false;
        this.sortField = '+lastUpdatedStamp';
      } else {
        this.upFlag2 = true;
        this.downFlag2 = false;
        this.sortField = '+lastUpdatedStamp';
      }
    }


    this.query();
  }


  // 分页
  paginate(event) {
    // 每页显示的条数;
    this.pageSize = event.rows;
    // 当前页
    this.pageNum = event.page + 1;
    this.first = event.first;
    // 调用查询的方法
    this.query();
  }

  // 默认查询事件
  query(isMessage?) {
    const params = {
      businessId: 'C0001'
    }
    params['pageNum'] = this.pageNum,// 当前页码
      params['pageSize'] = this.pageSize// 页面显示几条
    params['flag'] = '1' // 标志位  查询所有已经创建的策略
    params['sortField'] = this.sortField;
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: !isMessage,
      inputParam: params,
      message: this.messageService,
      injectName: 'strategyInfoListQuery',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.tableData = bodyData.dataList;
        this.totalNum = bodyData.totalNum
        console.log(bodyData)
      }
    });
  }

  // 操作按钮 判断显示弹窗
  showModal(optionName, item?) {
    item ? this.inValueItem = item : this.inValueItem = '';
    this.dialogDisplay = true;
    this.inValue = this.modalObj[optionName]
  }

  //    编辑策略的回调
  call(event) {
    this.dialogDisplay = false;
    if (event) {
      this.query(true);
    }
  }

  // 弹窗确认
  confirm(item) {
    this.confirmationService.confirm({
      message: '确认删除此条策略信息？',
      accept: () => {
        this.delete(item.strategyId);
      }
    });
  }

  // 删除
  delete(id) {
    const params = {
      businessId: 'C0001',
      strategyId: id
    };
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: true,
      inputParam: params,
      message: this.messageService,
      injectName: 'strategyInfoRemove',
      loadingShow: this.loadingShow,
      messageInfo: '删除成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.query(true);
      }
    });
  }

  // 跳转到搜索结果页面
  toSearchResult(data) {
    // console.log(data);
    // const queryParams = {
    //   businessId: 'C0001',
    //   strategyId: data.strategyId

    // }
    // const configParam = {
    //   httpService: this.httpService,
    //   loadingFlag: false,
    //   messageFlag: false,
    //   inputParam: queryParams,
    //   message: this.messageService,
    //   injectName: 'strategyInfoDetailQuery',
    //   loadingShow: this.loadingShow,
    //   messageInfo: '查询成功',
    // }
    // this.commonFunc.haddleData(configParam, (bodyData) => {
    //   if (bodyData) {
    //     const params = {
    //       businessId: 'C0001',    // 业务编号
    //       logicType: bodyData.logicType,    // 逻辑类型
    //       logicDesc: bodyData.logicDesc,    // 逻辑描述
    //       strategyName: bodyData.strategyName,
    //       strategyId: bodyData.strategyId,   //策略ID
    //       strategyContent: bodyData.strategyContent,  // 策略内容
    //       indicatorApplParam: bodyData.indicatorApplParam, 
    //       // queryRange: this.customType,  // 查询范围
    //       // dataDt: this.searchDate,  // 数据日期
    //       indicatorList: bodyData.indicatorList,  // 指标列表
    //     }
    //     this.router.navigate(['pages/tzb/custom/custom-search/search-result-pages'], {
    //       queryParams: { data: JSON.stringify(params) },
    //     });
    //   }
    // });

    const strategyId= this.commfunc.compileStr(data.strategyId);
    this.router.navigate(['/pages/tzb/custom/custom-search/search-result-pages',], { 
      queryParams:{ strategyId: strategyId,strategyFlag : '3'}
    })

  }

  // 跳转到报表
  selectTable(index) {
    this.router.navigate(['pages/tzb/custom/custom-search/custom-search-statistics'], {
      queryParams: {
        statisticsType: index
      }
    });
  }

  // 跳转到搜客主页
  selectIndex() {
    this.router.navigate(['pages/tzb/custom/custom-search']);
  }


  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}