import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { MessageService } from "primeng/components/common/messageservice";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { TZB_CUS_UPLOAD,TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { Message } from "primeng/primeng";

@Component({
  selector: 'app-everyone-search',
  templateUrl: './everyone-search.component.html',
  styleUrls: ['./everyone-search.component.scss'],
  providers: []
})
export class EveryoneSearchComponent implements OnInit {
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();

  data: any = [];// 后台返回数据

  numCeil: number = Math.ceil(this.data.length / 2); // 对后台返回数据的条数做处理，向上取整
  numFloor: number = Math.floor(this.data.length / 2); // 对后台返回数据的条数做处理，向上取整
  loadingShow: any = { flag: false }; // loading框

  params: any = {
    businessId: 'C0001',
    recommendType: '01'
  };
  uploadDisplay:boolean = false;
  UPLOAD: string = `${TZB_CUS_UPLOAD}?fileUrl=/customSearch&fileName=` + encodeURI('搜客数据口径') + '.pdf';
  fileList: any;
  msgs: Message[] = [];
  

  // flag : boolean = false;


  constructor(
    public router: Router,
    private messageService: MessageService,
    private httpService: CustomSearchHttpService,
    private commonFunc: Customtools,
    public commfunc: Commfunc,
  ) {

  }
  ngOnInit() {
    this.queryData(this.params, true);
  }

  // 大家都在搜查询
  queryData(params, isMessage?) {
    const configParam = {
      httpService: this.httpService,
      loadingFlag: false,
      messageFlag: !isMessage,
      inputParam: params,
      message: this.messageService,
      injectName: 'conditionRecommendListQuery',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        if(bodyData.dataList.length > 6){ // 如果返回数据大于6条 只取前六条
          this.data = bodyData.dataList.splice(0,6);
        }else{            // 如果返回数据小于等于于6条 直接赋值
          this.data = bodyData.dataList;
        }
        this.numCeil = Math.ceil(this.data.length / 2); // 对后台返回数据的条数做处理，向上取整
        this.numFloor = Math.floor(this.data.length / 2); // 对后台返回数据的条数做处理，向上取整
      }
    });
  }

  // 进入大家都在搜的配置页面
  toList() {
    this.router.navigate(['pages/tzb/custom/custom-search/custom-search-config'], {
      queryParams: {
         configType: 'everyone',
        data: JSON.stringify(this.data)
      }
    });
  }

  // 选择一条指标进行搜索
  selectItem(index) {
    const queryParams = {
      inputParams:{
        indicatorId:this.data[index].conditionId,
        sceneType: '002'
      }
      
    }
    const configParam = {
      httpService: this.httpService,
      loadingFlag: false,
      messageFlag: false,
      inputParam: queryParams,
      message: this.messageService,
      injectName: 'queryIndicatorApplScene',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.outValueEmit.emit(bodyData.outputParams.indicatorList[0])
      }
    });
  }
  customSearchUpload() {
		this.uploadDisplay = true;
	}

	customSearchDownload() {
		open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI('搜客数据口径.pdf') + "&fileUrl=/copdata/customSearch", '_blank')
	}

	// 上传点击,需跨域
	onBeforeSend(event) {
		if (event.xhr.response) {
			this.fileList = JSON.parse(event.xhr.response);
			if (this.fileList.hasOwnProperty('errorMsg')) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.errorMsg });
				return
			}
			this.msgs = [];
			this.msgs.push({ severity: 'success', summary: '提示', detail: '文件上传成功,文件名为搜客数据口径.pdf' });
		}
		else {
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '获取服务器列表失败！' }];
		}
	}
        //按钮权限
        permissionCheck(id) {
          return this.commfunc.permissionCheck(id)
      }
}