import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from "primeng/primeng";
@Component({
  selector: 'app-strategy-share',
  templateUrl: './strategy-share.component.html',
  styleUrls: ['./strategy-share.component.scss'],
  providers: []
})
export class StrategyShareComponent implements OnInit {
  @Input() inValueItem: any;
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();

  shareStrategyForm: FormGroup;  // 新增表单

  loadingShow: any = { flag: false }; // loading框

  mesg: any;

  orgList: any;

  chName: any;

  operatingLevel: string;  // 所属机构层级

  shareRangeList: any = []// 分享范围
  msgs: Message[] = [];
  display: boolean = false;
  strategyName1: string;
  strategyContent1: string;



  constructor(
    private fb: FormBuilder,
    private httpService: CustomSearchHttpService,
    private messageService: MessageService,
    private commonFunc: Customtools,
    public commfunc: Commfunc,
  ) {

  }
  ngOnInit() {
    this.shareRangeList = [
      // {value:'',label:'请选择'}
    ]
    this.queryOrg();      //  判断当前登录人的机构层级
    this.shareStrategyForm = this.fb.group({
      'strategyName': ['', [Validators.required]], // 策略名称
      'strategyId': [''], // 策略ID
      'shareRange': ['0', [Validators.required]], // 共享范围
      'strategyContent': [''], // 策略内容
    });
    this.shareStrategyForm.controls['strategyId'].setValue(this.inValueItem.strategyId);
    this.shareStrategyForm.controls['strategyName'].setValue(this.inValueItem.strategyName);
    this.shareStrategyForm.controls['strategyContent'].setValue(this.inValueItem.strategyContent);
    // this.query(); // 查询详情
  }


  queryOrg() {
    this.mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.chName = JSON.parse(window.sessionStorage.getItem('chName'));
    this.orgList = this.chName.orgList;
    this.orgList.forEach(el => {
      if (el.orgId === this.mesg.extParam.orgId) {
        this.operatingLevel = el.operatingLevel;
      }
    });
    this.setShareRange();


  }


  setShareRange() {//  根据所属机构  加分享范围
    switch (this.operatingLevel) {
      case 'GENERAL_BANK':
      case 'GROUP':
        this.shareRangeList.push({ value: '0', label: '全行' });
        break;
      case 'BRANCH':
      case 'BRANCH_DT':
        this.shareRangeList.push(
          { value: '0', label: '全行' },
          { value: '1', label: '本区域/分行' }
        );
        break;
      case 'SUB_BRANCH':
        this.shareRangeList.push(
          { value: '0', label: '全行' },
          { value: '1', label: '本区域/分行' },
          { value: '2', label: '本支行' }
        );

        break;
      case 'SUB_BRANCH_DT':
        this.shareRangeList.push(
          { value: '0', label: '全行' },
          { value: '1', label: '本区域/分行' },
          { value: '2', label: '本支行' },
          { value: '3', label: '本部门' }
        );

      default:
        break;
    }
  }

  // 默认查询事件
  query() {
    const params = {
      businessId: 'C0001',
      strategyId: this.inValueItem
    }
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: true,
      inputParam: params,
      message: this.messageService,
      injectName: 'strategyInfoDetailQuery',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.shareStrategyForm.controls['strategyName'].setValue(bodyData.strategyName);
        this.shareStrategyForm.controls['strategyContent'].setValue(bodyData.strategyContent);

      }
    });
  }


  // 保存
  save() {
    const params = this.shareStrategyForm.value;
    params['businessId'] = 'C0001'
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: true,
      inputParam: params,
      message: this.messageService,
      injectName: 'strategyInfoShareCreate',
      loadingShow: this.loadingShow,
      messageInfo: '分享成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.display = true;
        this.strategyName1 = params.strategyName;
        this.strategyContent1 = params.strategyContent;
      }
    });
  }

  close() {
    this.outValueEmit.emit(false);
  }

  onHide() {
    this.outValueEmit.emit(true);
  }

}