import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomSearchComponent } from 'app/pages/tzb/custom/custom-search/custom-search.component';
import { CustomSearchStatistics } from './custom-search-statistics/custom-search-statistics.component';
import { SearchIndexComponent } from "app/pages/tzb/custom/custom-search/search-index/search-index.component";
import { MyStrategyComponent } from "app/pages/tzb/custom/custom-search/my-strategy/my-strategy.component";
import { ConfigPageComponent } from "app/pages/tzb/custom/custom-search/config-page/config-page.component";
import { SearchResultPagesComponent } from "app/pages/tzb/custom/custom-search/search-result-pages/serach-result-pages.component";
import { customGroupSearchComponent } from "app/pages/tzb/custom/custom-search/custom-group-search/custom-group-search.component"


export const routes: Routes = [
    {path: '',component: SearchIndexComponent},
    { path: 'my-strategy', component: MyStrategyComponent },// 我的策略模块
    { path: 'custom-search-statistics', component: CustomSearchStatistics },// 搜客数据统计模块
    { path: 'custom-search-config', component: ConfigPageComponent },// 搜客配置页面
    { path: 'search-result-pages', component: SearchResultPagesComponent },// 搜索结果模块
    { path: 'custom-group-search', component: customGroupSearchComponent },// 群内高级搜索
];
export const CustomSearchRouting: ModuleWithProviders = RouterModule.forChild(routes);

