    // 查询策略详情
    queryDetail(injectName) {
        const params = {
            businessId: 'C0001',
            strategyId: this.strategyId
        }
        const configParam = {
            httpService: this.httpService,
            loadingFlag: false,
            messageFlag: false,
            inputParam: params,
            message: this.messageService,
            injectName: injectName,
            loadingShow: this.loadingShow,
            messageInfo: '查询成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                this.shareList = {
                    strategyId: bodyData.strategyId,
                    strategyName: bodyData.strategyName,
                    strategyContent: bodyData.strategyContent,
                }
                this.inValueItem = this.shareList;
                this.modalName1 = '分享策略'
                this.dialogDisplay1 = true;
                this.shareDisplay = true;
            }
        })
    };


    // 加入策略
    addStrategy() {
        if (!this.strategyName) {
            this.messageService.clear();
            this.messageService.add({ severity: 'error', summary: '提示', detail: '请输入策略名称' });
        } else {
            const params = {
                strategyName: this.strategyName,
                businessId: this.businessId,
                logicType: this.logicalRelationship.value,
                logicDesc: this.logicalRelationship.key,
                indicatorList: this.strategyList
            };
            if (this.logicalRelationship.value === '01') {
                params['logicDesc'] = '且'
            } else if (this.logicalRelationship.value === '02') {
                params['logicDesc'] = '或'
            }
            const configParam = {
                httpService: this.httpService,
                loadingFlag: true,
                messageFlag: true,
                inputParam: params,
                message: this.messageService,
                injectName: 'strategyInfoCreate',
                loadingShow: this.loadingShow,
                messageInfo: '加入成功',
            }
            this.commonFunc.haddleData(configParam, (bodyData) => {
                if (bodyData) {
                    this.strategyId = bodyData.strategyId;
                    this.shareConfirm();
                    this.isUpdate = '3';   // 加入为我的策略之后可以进行更新策略
                }
            });
        }
    }

    // 更新策略
    updateStrategy() {
        if (!this.strategyName) {
            this.messageService.clear();
            this.messageService.add({ severity: 'error', summary: '提示', detail: '请输入策略名称' });
        } else {
            const params = {
                businessId: this.businessId,
                strategyId: this.strategyId,
                strategyName: this.strategyName,
                logicType: this.logicalRelationship.value,
                logicDesc: this.logicalRelationship.key,
                indicatorList: this.strategyList
            };
            const configParam = {
                httpService: this.httpService,
                loadingFlag: true,
                messageFlag: true,
                inputParam: params,
                message: this.messageService,
                injectName: 'strategyInfoModify',
                loadingShow: this.loadingShow,
                messageInfo: '更新成功',
            }
            this.commonFunc.haddleData(configParam, (bodyData) => {
                if (bodyData) {
                    this.shareConfirm();
                }
            });
        }
    }

