    // 弹窗确认是否删除查询条件
    deleteConfirm(index) {
        this.confirmationService.confirm({
            message: `确认删除此条策略条件？(策略条件顺序可能发生变化，请注意逻辑关系)`,
            // ` + '<br>' + `
            accept: () => {
                this.strategyList.splice(index, 1);
                this.strategyList.forEach(el => {
                    if (el.seqId > index + 1) {
                        el.seqId = el.seqId - 1;
                    }
                })
                this.pageNum = 1,
                    this.pageSize = 20
                this.selectAllShowIndicator()   // 调用组合展示所有指标的方法
            }
        });
    }

