    // 提交申请
    toCommit() {
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let postId = commonHeader.extParam.postId;     // 任职岗位
        let downFlag = false;       // 判断是否需要走审批
        for (let item of this.downPostId) {
            if (item.value === postId) {
                downFlag = true;
            }
        }
        if (downFlag) { // 判断是否为无需审批的岗位
            this.downMessageInfo = '数据已准备完毕，请至下载中心下载';
            this.searchResultExport();// 没有审批节点  直接执行高级搜索结果导出方法
        } else {// 非客户经理走选人界面选择审批人
            this.waitSelUserList = this.commfunc.query('XZZXLC', {
                lctybl: commonHeader.extParam.postId
            });
            if (!this.waitSelUserList.erro) {
                this.outVal = this.waitSelUserList.waitSelUserList;
                let chooseThis = this.waitSelUserList.display;
                let a2 = commonHeader.extParam.specifyNextOpers;
                if (this.waitSelUserList.display) {
                    this.dealPerShow = true;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
            }
        }
    }


    outDisplayCall(event) {
        this.dealPerShow = event;
    }
    outValueCall(event) {
        let ss = '';
        event.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.downMessageInfo = '审批通过后，请至下载中心下载';
        this.searchResultExport();  // 选人结束  执行高级搜索结果导出方法
    }

    // 搜客报表结果导出
    searchResultExport() {
        const params = {
            businessId: this.businessId,
            queryRange: this.indexShow + 1 + '',
            dataDt: this.chooseMonthMain,
            logicType: this.logicalRelationship.value,
            logicDesc: this.logicalRelationship.key,
            indicatorList: this.strategyList
        };
        params['outputFieldList'] = this.downloadList;
        const configParam = {
            httpService: this.httpService,
            loadingFlag: true,
            messageFlag: true,
            inputParam: params,
            message: this.messageService,
            injectName: 'advancedSearchResultExport',
            loadingShow: this.loadingShow,
            messageInfo: this.downMessageInfo,
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
            }
        });
    }




