import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { MessageService } from "primeng/components/common/messageservice";
import { Message } from "primeng/components/common/message";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";

@Component({
    selector: 'app-select-invest',
    templateUrl: './select-invest.component.html',
    styleUrls: ['./select-invest.component.scss'],
    providers: []
})
export class SelectInvestComponent implements OnInit {
    @Input() inValueItem: any;
    @Output() outValueEmit: EventEmitter<any> = new EventEmitter();

    selectInvestForm: FormGroup;  // 新增表单

    resultList: any[];

    selectedFiles: any = [];

    sendData: any = [];  // 发送回去的数据

    loadingShow: any = { flag: false }; // loading框

    pageSize: number = 10;
    pageNum: number = 1;
    totalNum: number;
    first: any = 0;
    msgs: any[];
    tabMesg: any = '暂无数据';
    tempArr: any = [];

    selectedCar: any = [];   // 选中数据
    checkAll: boolean = false;
    taskTypeName1: string;
    taskTypeName2: string;
    taskTypeName3: string;
    taskTypeName4: string;

    constructor(
        private messageService: MessageService,
        private httpService: CustomSearchHttpService,
        private commonFunc: Customtools,
        private fb: FormBuilder,
        public CusOperationHttpService: CusOperationHttpService,
    ) {

    }
    ngOnInit() {
        this.query();   // 初始化查询事件
        this.select(); // 反选事件
    }
    select() {
        if (this.inValueItem && this.inValueItem != '') {
            this.tempArr = this.inValueItem.split(',')
            this.tempArr.forEach(el => {
                let param = {
                    taskTypeId: el
                }
                this.CusOperationHttpService.exportIndustryDictionaryLevelTree(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.selectedCar.push(data.resultList[0])
                        console.log(this.selectedCar);
                    } else {
                        this.msgs = [];
                        data.returnCode.code ? data.returnCode.code : '查询失败！';
                        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
                })
            });
        }
    }

    query() {
        let param = {
            pageSize: this.pageSize,
            pageNum: this.pageNum
        }
        this.taskTypeName1 ? param['taskTypeName'] = this.taskTypeName1 : param['taskTypeName'] = '';
        this.taskTypeName2 ? param['taskTypeName'] = this.taskTypeName2 : param['taskTypeName'] = param['taskTypeName'];
        this.taskTypeName3 ? param['taskTypeName'] = this.taskTypeName3 : param['taskTypeName'] = param['taskTypeName'];
        this.taskTypeName4 ? param['taskTypeName'] = this.taskTypeName4 : param['taskTypeName'] = param['taskTypeName'];
        if (this.taskTypeName1) {
            // && !this.taskTypeName2 && !this.taskTypeName3 && !this.taskTypeName4
            param['taskTypeLevel'] = '1'
        }
        if (this.taskTypeName2) {
            param['taskTypeLevel'] = '2'
        }
        if (this.taskTypeName3) {
            param['taskTypeLevel'] = '3'
        }
        if (this.taskTypeName4) {
            param['taskTypeLevel'] = '4'
        }
        this.CusOperationHttpService.exportIndustryDictionaryLevelTree(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.resultList = data.resultList;
                this.totalNum = data.totalNum;
            } else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }

    doQuery() {
        this.first = 0;
        this.pageNum = 1;
        this.pageSize = 10;
        this.query();
    }
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = event.first;
        this.query();
    }

    reset() {
        this.taskTypeName1 = '';
        this.taskTypeName2 = '';
        this.taskTypeName3 = '';
        this.taskTypeName4 = '';
    }

    // 清空
    back() {
        this.selectedCar = [];
        this.sendData = [];
    }

    submit() {
        console.log(this.selectedCar);
        this.selectedCar.forEach(el => {
            if (el.taskTypeId) {
                this.sendData.push(el)
            }
        });
        this.outValueEmit.emit(this.sendData);

        console.log(this.sendData);


    }

}