import { Component, OnInit } from '@angular/core';
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { ConfirmationService } from 'primeng/primeng';
import { Router , ActivatedRoute } from '@angular/router';
import { Message } from "primeng/components/common/message";
@Component({
  selector: 'app-master-config',
  templateUrl: './master-config.component.html',
  styleUrls: ['./master-config.component.scss'],
  providers: []
})
export class MasterConfigComponent implements OnInit {

  dialogDisplay: boolean = false;

  data: any = [
    {}, {}, {}, {}
  ];

  index: any;

  index_: any;

  msgs: Message[] = [];

  loadingShow: any = { flag: false }; // loading框

  personPage: boolean = false; // 选择创建人弹窗

  messageFlag: boolean = false;

  saveFlag:boolean = true;

  queryParams:any;

  constructor(
    private httpService: CustomSearchHttpService,
    private messageService: MessageService,
    private commonFunc: Customtools,
    private confirmationService: ConfirmationService,
    public router: Router,
    private activeRoute: ActivatedRoute,
  ) {

  }
  ngOnInit() {
    this.activeRoute.queryParams.subscribe(params => {
      this.queryParams = JSON.parse(params.data);
      console.log(this.queryParams);
      for (let i = 0; i < this.queryParams.length; i++) {
        if(this.queryParams[i]){
          this.data[i] = this.queryParams[i]
        }
        
      }
  })
  console.log(this.data)
  }

  selectDecision(i) {
    this.dialogDisplay = true;
    this.index = i;
  }

  // 选择策略之后的回调函数
  call(event) {
    this.dialogDisplay = false;
    console.log(event);
    if (event) {
      this.data[this.index].conditionName = event.strategyName;
      this.data[this.index].conditionId = event.strategyId;
      this.data[this.index].seqId = this.index + 1 + "";
    }

  }

  // 切换radiobutton
  selsectRadio(item, i) {
    if (item === 'isNew') {
      this.data[i].isNew = '1';
      this.data[i].isTopSearch = '0';
    } else if (item === 'isTopSearch') {
      this.data[i].isNew = '0';
      this.data[i].isTopSearch = '1';
    }
  }

  // 保存配置
  save() {
    this.saveFlag = true;
    const params = {
      businessId: 'C0001',
      recommendType: '02',
      dataList: []
    }
   try {  // 需求为选择了策略则必须输入推荐人，因此需要在策略ID存在的前提下，判断是否输入了推荐人，如果推荐人为空，则以抛异常的形式结束当前循环。
    this.data.forEach((el,index) => {
      if(el.conditionId){
        if(el.recommendUserName){
          params.dataList.push(el);
        }else{
          this.saveFlag = false;
          this.messageService.clear();
          this.messageService.add({ severity: 'error', summary: '提示', detail: `请完善第${index + 1}条数据` });
          throw new Error();
        }
      }
    });
   } catch (error) {
     
   }
   console.log(params)
    const configParam = {
      httpService: this.httpService,
      loadingFlag: false,
      messageFlag: true,
      inputParam: params,
      message: this.messageService,
      injectName: 'conditionRecommendSave',
      loadingShow: this.loadingShow,
      messageInfo: '保存成功',
    }
    if(this.saveFlag){
      this.commonFunc.haddleData(configParam, (bodyData) => {
        if (bodyData) {
          this.router.navigate(['pages/tzb/custom/custom-search']);
        }
      });
    }

  }

  back() {
    this.router.navigate(['pages/tzb/custom/custom-search']);
  }

  delete(){
    this.data = [
      {}, {}, {}, {}
    ];
  }

}