import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
@Component({
  selector: 'app-update-strategy',
  templateUrl: './update-strategy.component.html',
  styleUrls: ['./update-strategy.component.scss'],
  providers: []
})
export class UpdateStrategyComponent implements OnInit {
  @Input() inValueItem: any;
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();
  
  updateStrategyForm: FormGroup;  // 修改表单

  list:any;

  loadingShow: any = { flag: false }; // loading框


  constructor(
    private fb: FormBuilder,
    private httpService: CustomSearchHttpService,
    private messageService: MessageService,
    private commonFunc: Customtools
  ) {

  }
  ngOnInit() {
    this.updateStrategyForm = this.fb.group({
      'businessId':['C0001'], //业务编号
      'strategyId':[''], // 策略编号
      'strategyName': ['', [Validators.required]], // 策略名称
      'strategyContent':[''], // 策略内容
      'logicType':[''], // 逻辑类型
      'logicDesc':[''], // 逻辑描述
      'lastQueryCustCount':[''], // 上次查询客户数量
      'lastQueryTime':[''], // 上次查询时间
      'lastUpdateTime':[''], // 上次修改时间
      'createdStamp':[''],  // 收藏时间
    });
    
    // this.putValue();
    this.query();
    

  }


  putValue(){

  }
  

  默认查询事件
    query() {
      const params = {
        businessId: 'C0001',
        strategyId:this.inValueItem.strategyId
      }
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: true,
      inputParam: params,
      message: this.messageService,
      injectName: 'strategyInfoDetailQuery',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.list = bodyData.indicatorList;
      this.updateStrategyForm.controls['strategyId'].setValue(bodyData.strategyId);
      this.updateStrategyForm.controls['strategyName'].setValue(bodyData.strategyName);
      this.updateStrategyForm.controls['lastQueryCustCount'].setValue(this.inValueItem.lastQueryCustCount);
      this.updateStrategyForm.controls['lastQueryTime'].setValue(this.inValueItem.lastQueryTime);
      this.updateStrategyForm.controls['lastUpdateTime'].setValue(this.inValueItem.lastUpdateTime);
      this.updateStrategyForm.controls['createdStamp'].setValue(this.inValueItem.createdStamp);
      this.updateStrategyForm.controls['strategyContent'].setValue(this.inValueItem.strategyContent);
        // 为逻辑关系赋值
      if(bodyData.logicType === '01'){
        this.updateStrategyForm.controls['logicDesc'].setValue('且');
      }else if(bodyData.logicType === '02'){
        this.updateStrategyForm.controls['logicDesc'].setValue('或');
      }else if(bodyData.logicType === '03'){
        this.updateStrategyForm.controls['logicDesc'].setValue(bodyData.logicDesc);
      }
      console.log(this.updateStrategyForm);
      }
    });
  }

    // 保存
    save() {
      const params = this.updateStrategyForm.value;
      params.indicatorList = this.list;
      const configParam = {
        httpService: this.httpService,
        loadingFlag: true,
        messageFlag: true,
        inputParam: params,
        message: this.messageService,
        injectName: 'strategyInfoModify',
        loadingShow: this.loadingShow,
        messageInfo: '更新成功',
      }
      this.commonFunc.haddleData(configParam, (bodyData) => {
        if (bodyData) {
          this.outValueEmit.emit(true);
        }
      });
    }

  close(){
    this.outValueEmit.emit(false);
  }

}