import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { MessageService } from "primeng/components/common/messageservice";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Message } from "primeng/components/common/message";


@Component({
  selector: 'app-select-logic',
  templateUrl: './select-logic.component.html',
  styleUrls: ['./select-logic.component.scss'],
  providers: []
})
export class SelectLogicComponent implements OnInit {
  @Input() inValueLogic:any;
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();
  
  logic:any = {
    key:'', // 自定义的逻辑表达式
    value:'01' // 逻辑类型
  }
  msgs: Message[] = [];
  // logicFlag:boolean = false;  // 是否需要调用逻辑关系的校验接口

  dialogDisplay:boolean = false;  // 不会编辑点这里

  loadingShow: any = { flag: false }; // loading框


  constructor(
    private messageService: MessageService,
    private httpService: CustomSearchHttpService,
    private commonFunc: Customtools,
  ) {

  }
  ngOnInit() {
    this.logic = this.inValueLogic;

  }




// 保存逻辑关系
  save(){
    
    if(this.logic.key === '' && this.logic.value === '03'){
      this.messageService.add({ severity: 'error', summary: '提示', detail: `请完善逻辑关系` });
    }else if(this.logic.key !== '' && this.logic.value === '03'){
      this.checkLogicalExpression();
    }else{
      this.outValueEmit.emit(this.logic);
    }
  }

  close(){
    this.outValueEmit.emit(false);
  }

  // clickTrue(flag){
  //   this.logicFlag = flag;
  // }

  // 校验逻辑关系是否正确
  checkLogicalExpression(){
    const params = {
      logicalExpression:this.logic.key
    }
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: false,
      inputParam: params,
      message: this.messageService,
      injectName: 'checkLogicalExpression',
      loadingShow: this.loadingShow,
      messageInfo: '保存成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
       if(bodyData.result === 'true'){
        this.messageService.add({ severity: 'success', summary: '提示', detail: `保存成功` });
        this.outValueEmit.emit(this.logic);
       }else  if(bodyData.result === 'false'){
        this.messageService.add({ severity: 'error', summary: '提示', detail: `请输入合理的逻辑关系` });
       }

      }
    });
  }


  

    //不会编辑的事件
    doedit(){
      this.dialogDisplay = true;
    }

  back(){
    this.dialogDisplay = false;
  }
}