import { Component, OnInit } from '@angular/core';
import { MessageService } from "primeng/components/common/messageservice";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Router } from '@angular/router';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'app-share-dynamic',
  templateUrl: './share-dynamic.component.html',
  styleUrls: ['./share-dynamic.component.scss'],
  providers: []
})
export class ShareDynamicComponent implements OnInit {

  data:any = [];  // 主页策略列表

  strategyName:string;
  shareUserId:string;
  more:string = '....';

  loadingShow: any = { flag: false }; // loading框
  
    params: any = {
      businessId: 'C0001'
    };

  tabTable:any = [
    {strategyName:'213',shareUserId:'222'},
    {strategyName:'213',shareUserId:'222'},
    {strategyName:'213',shareUserId:'222'},
    {strategyName:'213',shareUserId:'222'},
    {strategyName:'213',shareUserId:'222'},
    {strategyName:'213',shareUserId:'222'},
    {strategyName:'213',shareUserId:'222'},
  ]; // 弹窗分享的策略列表

  dialogDisplay:boolean = false;  // 分享策略列表

  pageSize = 10; // 页面显示几条
  pageNum = 1; // 当前页码
  totalNum: any;  // 总条数

  constructor(
    private messageService: MessageService,
    private httpService: CustomSearchHttpService,
    private commonFunc: Customtools,
    public router: Router,
    public commfunc: Commfunc,
  ) {

  }
  ngOnInit() {
    this.queryData(this.params,true);
  }

  
  
  // 分享动态查询
  queryData(params, isMessage?) {
    const configParam = {
      httpService: this.httpService,
      loadingFlag: false,
      messageFlag: !isMessage,
      inputParam: params,
      message: this.messageService,
      injectName: 'strategyInfoShareDynamicQuery',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        if(bodyData.dataList.length > 3){ // 如果返回数据大于4条 只取前3条
          this.data = bodyData.dataList.splice(0,3);
        }else{            // 如果返回数据小于等于于3条 直接赋值
          this.data = bodyData.dataList;
        }

      }
    });
  }

  // 展示分享策略的列表
  showList(){
    this.dialogDisplay = true;
  }

  // 返回按钮
  call(event) {
    this.dialogDisplay = false;
  }

  
  // 重置按钮
  reset(){
    this.strategyName = '';
    this.shareUserId = '';
  }

  // 查询按钮
  query(){

  }

  selectCustom(data){
    const strategyId= this.commfunc.compileStr(data.strategyId);
    this.router.navigate(['/pages/tzb/custom/custom-search/search-result-pages',], { 
      queryParams:{ strategyId: strategyId,strategyFlag : '1'}
    })
  }

  mouseover(){
    this.more = '更多';
  }

  mouseout(){
    this.more = '....'
  }


}