import { OnInit, Component, Output, EventEmitter, Input } from "@angular/core";
import { FormGroup, FormBuilder } from "@angular/forms";
import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Message } from "primeng/primeng";

@Component({
    selector: 'app-more-target',
    templateUrl: './more-target.component.html',
    styleUrls: ['./more-target.component.scss'],
    providers: []
})

export class MoreTargetComponent implements OnInit {
    @Input() inValueItem: any;
    @Output() outValueEmit: EventEmitter<any> = new EventEmitter();
    msgs: Message[] = [];

    selectIndicatorForm: FormGroup;  // 查询表单

    tabTable: any = []    // 查询结果

    selectedCar: any = [];  // 列表选中数据集合

    sendData: any = [];  //  发送给查询页面的客户群数据集合

    loadingShow: any = { flag: false }; // loading框

    pageSize: number = 10;//每页条数
    pageNum: number = 1;//当前页数
    totalNum: number;
    first: number = 0;
    tableMesg: any = tableMessage;
    checkLittle: boolean = false;
    checkAll: boolean = false;
    tempArr: any = [];


    constructor(
        private messageService: MessageService,
        private httpService: CustomSearchHttpService,
        private commonFunc: Customtools,
        private commfunc: Commfunc,
        private fb: FormBuilder,
    ) {
    }
    ngOnInit() {
        // this.queryName('客户号');
        // this.queryName('客户名称');
        this.selectIndicatorForm = this.fb.group({
            'indicatorId': [''],
            'indicatorName': [''],
        });
        this.inValue(); // 反选已经在表头的指标
        this.query();
    }

    inValue() {

        this.inValueItem.forEach(el => {
            // this.tempArr.push(el.indicatorId)
            // console.log(this.tempArr);
            // el.isChecked = true;
            this.selectedCar.push(el);
            // {
            //     indicatorId : el.id,
            //     indicatorName: el.label,
            //     isChecked : true
            // }
        });
    }

    // 点击查询
    doquery() {
        this.first = 0;
        this.pageNum = 1;
        this.query();
    }
    // 重置
    reset() {
        this.selectIndicatorForm.reset();
    }

    // 分页
    paginate(event) {
        // 每页显示的条数;
        this.pageSize = event.rows;
        // 当前页
        this.pageNum = event.page + 1;
        this.first = event.first;
        // 调用查询的方法
        this.query();
    }

    // 查询指标列表
    query() {

        const params = {
            inputParams: { pageSize: this.pageSize, pageNum: this.pageNum, sceneType: '002', status: '1', isShow: ['1', '3'], acOrderBy: ['remark1 ASC'] }
        }
        for (var key in this.selectIndicatorForm.value) {
            if (this.selectIndicatorForm.value[key]) {
                params.inputParams[key] = this.selectIndicatorForm.value[key]
            }
        }
        const configParam = {
            httpService: this.httpService,
            loadingFlag: false,
            messageFlag: false,
            inputParam: params,
            message: this.messageService,
            injectName: 'queryIndicatorApplScene',
            loadingShow: this.loadingShow,
            messageInfo: '查询成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                bodyData.outputParams.indicatorList.forEach((el, index) => {
                    if (el.indicatorName == '客户号' || el.indicatorName == '客户名称') {
                        bodyData.outputParams.indicatorList.splice(index, 1);
                    }
                });
                this.tabTable = bodyData.outputParams.indicatorList;
                this.totalNum = bodyData.outputParams.totalNum;
            }
        });
    }


    submit() {
        this.outValueEmit.emit(this.selectedCar);
        console.log(this.selectedCar);
        this.selectedCar = [];
    }


    // 最多选择五十个字段
    onRowSelect(event) {
        console.log(event);
        if (this.selectedCar.length > 49) {
            this.selectedCar.forEach((el, index) => {
                if (el.indicatorId == event.data.indicatorId) {
                    this.selectedCar.splice(index, 1);
                }
            });
            this.messageService.clear();
            this.messageService.add({ severity: 'error', summary: '提示', detail: '展示字段最多选择50个' });
        }
        console.log(this.selectedCar);
    }

    // 查询默认展示的指标
    queryShowTarget() {
        this.checkLittle = false;
        this.selectedCar = [];
        const params = {
            inputParams: { sceneType: '002', status: '1', isShow: ['1', '3'], acOrderBy: ['remark1 ASC'] }
        };
        const configParam = {
            httpService: this.httpService,
            loadingFlag: true,
            messageFlag: false,
            inputParam: params,
            message: this.messageService,
            injectName: 'queryIndicatorApplScene',
            loadingShow: this.loadingShow,
            messageInfo: '恢复成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                bodyData.outputParams.indicatorList.forEach(el => {
                    if (el.indicatorName == '联系号码' || el.indicatorName == '存款余额' || el.indicatorName == '存款本月日均' || el.indicatorName == '存款前推六个月日均' ||
                        el.indicatorName == '存款上年日均' || el.indicatorName == '贷款授信金额' || el.indicatorName == '贷款余额(含核销)' || el.indicatorName == '贷款上年日均' ||
                        el.indicatorName == '贷款前推六个月日均' || el.indicatorName == '持有产品' || el.indicatorName == '理财余额' || el.indicatorName == '信用卡透支余额') {
                        this.selectedCar.push(el)
                    }
                });
                this.doquery()
            }
        });

    }
}