import { Component, OnInit, Output, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { Router, ActivatedRoute } from '@angular/router';
// json类的获取
// table的类
import { UsageQuantityTable, TargetQuantityTable, StrategyStatisticsTable } from './statistics-bean/statistics-table-data';
import { StrategyDetailsTable, SharingStrategyDetailsTable } from './statistics-bean/statistics-table-data';
// search的类
import { UsageQuantitySearch, TargetQuantitySearch, StrategyStatisticsSearch } from './statistics-bean/statistics-search-data';
import { StrategyDetailsSearch, SharingStrategyDetailsSearch } from './statistics-bean/statistics-search-data';
import { MessageService } from "primeng/components/common/messageservice";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { TZB_CUS_DOWNLOAD } from "app/@uisftech/common/service/http.constant";
import * as _ from 'lodash';



@Component({
    selector: "app-custom-search-statistics",
    templateUrl: './custom-search-statistics.component.html',
    styleUrls: ['./custom-search-statistics.component.scss']
})

export class CustomSearchStatistics implements OnInit {
    statisticsType: any;//判断查询表的标识
    headtitle: any;//判断查询表名的标识
    searchdata: any = [];//查询表变量值的
    tableHead: any;

    DOWNLOAD: string = TZB_CUS_DOWNLOAD;

    code: any;
    ch: any = CALENDAR_CN;
    msgs: any;

    startTime: Date = null;
    endTime: Date = null;
    dataTable: any[];//后端返回表的数据变量
    first: any = 1;
    total: number;
    pageNum: number = 1;
    pageSize: number = 10;
    injectNameList: any = [     // 调用接口名
        'advancedSearchUseCountReportQuery',  // 高级搜索使用量统计列表查询
        'indicatorQueryCountReportQuery',// 指标查询量统计列表查询
        'strategyCountListQuery',   // 策略统计列表查询
        'strategyDetailListQuery',  // 我的策略明细查询
        'strategyInfoShareListQuery',   // 策略共享列表查询
    ]
    loadingShow: any = { flag: false }; // loading框

    startDate: string;   // 起始日期
    endDate: string; // 截止日期

    injectName: string;

    // 页头标题循环数据的存储变量
    statisticshead: any[] = [{
        label: '搜客使用量统计查询',
        value: 1,
    }, {
        label: '搜客指标查询量统计查询',
        value: 2,
    }, {
        label: '搜客我的策略统计查询',
        value: 3,
    }, {
        label: '搜客我的策略明细查询',
        value: 4,
    }, {
        label: '搜客分享策略明细查询',
        value: 5,
    }];

    personPage: boolean = false; // 选择员工弹窗

    orgDisplay: boolean = false; // 机构模态框

    userId: string;  // 员工Id;

    orgId: string; // 机构Id

    isSencod: boolean = false;

    report: string;

    constructor(
        public router: Router,
        public commfunc: Commfunc,
        public activatedRoute: ActivatedRoute,
        private route: ActivatedRoute,
        private messageService: MessageService,
        private httpService: CustomSearchHttpService,
        private commonFunc: Customtools,
    ) {
        this.codeValues();
    }

    ngOnInit() {
        this.statisticsType = this.route.snapshot.queryParams['statisticsType'];
        this.statisticsType = Number(this.statisticsType);
        this.injectName = this.injectNameList[this.statisticsType - 1]     // 调用的接口名称
        // 1 - 搜客使用量统计查询 2 - 搜客指标查询量统计查询 3 - 搜客我的策略统计查询 4 - 搜客我的策略明细查询 5 - 搜客分享策略明细查询
        this.headtitle = this.statisticsType;
        this.changeTable();
        this.queryList(this.injectName);
    }

    changeTable() {
        // debugger
        if (this.statisticsType === 1) {
            UsageQuantitySearch.forEach(el => {
                el.SearchVal = '';
            });
            this.searchdata = _.clone(UsageQuantitySearch);
            this.tableHead = UsageQuantityTable;
            this.startTime = new Date(this.queryDate().firstDate);  // 默认开始时间为上月初
            this.endTime = new Date(this.queryDate().lastDate); // 默认结束时间为上月末
            this.report = '2';

        } else if (this.statisticsType === 2) {
            TargetQuantitySearch.forEach(el => {
                el.SearchVal = '';
            });
            this.searchdata = _.clone(TargetQuantitySearch);
            this.tableHead = TargetQuantityTable;
            this.isSencod = true;
            this.startTime = new Date(this.queryDate().firstDate);  // 默认开始时间为上月初
            this.endTime = new Date(this.queryDate().lastDate); // 默认结束时间为上月末
            this.report = '4';

        } else if (this.statisticsType === 3) {
            StrategyStatisticsSearch.forEach(el => {
                el.SearchVal = '';
            });
            this.searchdata = _.clone(StrategyStatisticsSearch);
            this.tableHead = StrategyStatisticsTable;
            this.startTime = new Date(this.queryDate().firstDate);  // 默认开始时间为上月初
            this.endTime = new Date(this.queryDate().lastDate); // 默认结束时间为上月末
            this.report = '1';

        } else if (this.statisticsType === 4) {
            StrategyDetailsSearch.forEach(el => {
                el.SearchVal = '';
            });
            this.searchdata = _.clone(StrategyDetailsSearch);
            this.tableHead = StrategyDetailsTable;
            this.startTime = new Date(this.queryDate().nowDate);  // 默认开始时间为上月初
            this.endTime = new Date(this.queryDate().nowDate); // 默认结束时间为上月末
            this.report = '3';

        } else if (this.statisticsType === 5) {
            SharingStrategyDetailsSearch.forEach(el => {
                el.SearchVal = '';
            });
            this.searchdata = _.clone(SharingStrategyDetailsSearch);
            this.tableHead = SharingStrategyDetailsTable;
            this.startTime = new Date(this.queryDate().nowDate);  // 默认开始时间为上月初
            this.endTime = new Date(this.queryDate().nowDate); // 默认结束时间为上月末
            this.report = '5';
        }
    }


    // 导出
    exportTable() {
        const params = {
            businessId: 'C0001',
            report: this.report,
        }
        if (this.startTime != null) {
            params['startDate'] = this.commfunc.transDate(this.startTime)
        }
        if (this.endTime != null) {
            let tempEndTime = this.commfunc.DateAdd('d', 1, this.endTime, 'add');
            params['endDate'] = this.commfunc.transDate(tempEndTime)
        }
        switch (this.statisticsType) {
            case 1:
                params['createUserId'] = this.userId;
                params['createOrgId'] = this.orgId;
                break;
            case 2:
                params['indicatorName'] = this.searchdata[0].SearchVal;
                params['createOrgId'] = this.orgId;
                break;
            case 3:
                params['createUserId'] = this.userId;
                params['createOrgId'] = this.orgId;
                break;
            case 4:
                params['createUserId'] = this.userId;
                params['createOrgId'] = this.orgId;
                params['strategyName'] = this.searchdata[2].SearchVal;
                break;
            case 5:
                params['createUserId'] = this.userId;
                params['createOrgId'] = this.orgId;
                params['strategyName'] = this.searchdata[2].SearchVal;
                break;
            default:
                break;
        }

        const configParam = {
            httpService: this.httpService,
            loadingFlag: false,
            messageFlag: true,
            inputParam: params,
            message: this.messageService,
            injectName: 'strategyListQueryResultExport',
            loadingShow: this.loadingShow,
            messageInfo: '下载成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                window.open(this.DOWNLOAD + "?fileName=" + bodyData.fileName + "&fileUrl=" + bodyData.fileUrl)
                //下载模板
                // downLoadTemp(){
                //     open(this.DOWNLOAD + "?fileName=" + encodeURI("updateCustInfo.xls") + "&fileUrl=" + "/copdata/moban/", "_blank");
                // }

            }
        });

    }
    // 初始化查询表/查询按钮方法
    query() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.queryList(this.injectName)
    }
    // 重置按钮方法
    clear() {
        this.foefor()
        this.pageSize = 10;
        this.pageNum = 1;
        this.queryList(this.injectName)
    }
    // 接口查询方法
    queryList(injectName) {

        const params = {
            businessId: 'C0001',
            pageNum: this.pageNum,
            pageSize: this.pageSize,
        }
        if (this.startTime != null) {
            params['startDate'] = this.commfunc.transDate(this.startTime)
        }
        if (this.endTime != null) {
            let tempEndTime = this.commfunc.DateAdd('d', 1, this.endTime, 'add');
            params['endDate'] = this.commfunc.transDate(tempEndTime)
        }
        switch (this.statisticsType) {
            case 1:
                params['userId'] = this.userId;
                params['orgId'] = this.orgId;
                break;
            case 2:
                params['indicatorName'] = this.searchdata[0].SearchVal;
                params['orgId'] = this.orgId;
                break;
            case 3:
                params['createUserId'] = this.userId;
                params['createOrgId'] = this.orgId;
                break;
            case 4:
                params['createUserId'] = this.userId;
                params['createOrgId'] = this.orgId;
                params['strategyName'] = this.searchdata[2].SearchVal;
                break;
            case 5:
                params['shareUserId'] = this.userId;
                params['shareOrgId'] = this.orgId;
                params['strategyName'] = this.searchdata[2].SearchVal;
                break;
            default:
                break;
        }
        const configParam = {
            httpService: this.httpService,
            loadingFlag: false,
            messageFlag: true,
            inputParam: params,
            message: this.messageService,
            injectName: injectName,
            loadingShow: this.loadingShow,
            messageInfo: '查询成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                bodyData.dataList.forEach(el => {
                    if (el.logicType && el.logicType == '01') {       // 将逻辑关系转码
                        el['logicContant'] = '且'
                    } else if (el.logicType && el.logicType == '02') {
                        el['logicContant'] = '或'
                    } else if (el.logicType && el.logicType == '03') {
                        el['logicContant'] = el['logicDesc']
                    }
                });
                console.log(bodyData.dataList);
                this.dataTable = bodyData.dataList;
                this.total = bodyData.totalNum;
            }
        });
    }

    //页面执行清空类的历史赋值
    foefor() {
        let ss = this.searchdata;
        for (let i = 0; i < ss.length; i++) {
            ss.forEach(e => {
                e.SearchVal = '';
            });
        }
        this.userId = '';
        this.orgId = '';
        this.startTime = null;
        this.endTime = null;
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
    // 分页
    paginate(event) {
        console.log(event)
        // 每页显示的条数;
        this.pageSize = Number(event.rows);
        // 当前页
        this.pageNum = event.page + 1;
        this.first = event.first;
        // 调用查询的方法
        this.queryList(this.injectName);
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }


    // 选择创建人
    selectCreateor() {
        this.personPage = true;
    }


    selectOrg() {
        // 选择机构
        this.orgDisplay = true;
    }

    // 选择创建人之后的回调函数
    workerCall(event) {
        this.personPage = false;
        if (event.length > 0) {
            this.searchdata[0].SearchVal = event[0].tellerName;
            this.userId = event[0].tellerId;
        }

    }

    // 选择机构之后的回调函数
    returnOrg(event) {
        if (event.hasOwnProperty('orgName')) {
            this.searchdata[1].SearchVal = event.orgName;
            this.orgId = event.orgId;
        }
        this.orgDisplay = false;
    }

    back() {
        this.router.navigate(['pages/tzb/custom/custom-search/my-strategy']);
    }


    // 查询出上月初和上月末的时间
    queryDate() {
        let nowdays = new Date();
        let year = nowdays.getFullYear();
        let month = nowdays.getMonth();
        let month_ = month + 1;
        let date = nowdays.getDate();
        let nowDate = year + '-' + month_ + '-' + date;
        if (month == 0) {
            month = 12;
            year = year - 1;
        }
        if (month > 0) {
            month = 0 + month;
        }
        let firstDay = year + '-' + month + '-' + '01';
        let myDate = new Date(year, month, 0);
        let lastDay = year + '-' + month + '-' + myDate.getDate();
        return {
            nowDate: nowDate,
            firstDate: firstDay,
            lastDate: lastDay
        }
    }

}