export class PaginateBean {
    first: number = 1;       //初始页
    pageNum: number = 1;     //页数
    pageSize: number = 1;    //每页展示条数
    pageLinkSize: number = 1;//展示几页
    rowsPerPageOptions: any[] = [10, 20, 50, 200];//可选每页展示条数
    total: number;           //总条数
}

export class StrategyInfoBean {
    strategyName: String;	 //策略名称	
    businessId: String;	     //业务编号	
    logicType: String;	     //逻辑类型	
    logicDesc: String;	     //逻辑描述	
    indicatorList: any[];	 //指标列表
    seqId: String;           //序号	
    indicatorId: String;     //指标编号	
    indicatorName: String;   //指标名称	
    showType: String;	     //展示类型	
    valueType: String;       //取值类型	
    indicatorValue: String;	 //指标值
    indicatorValueCn: String;//指标值中文
    description: String;	 //描述	
}

