  // 高级搜索执行
  doSearch() {
    let arrTemp = [];
    let statisticFlag = false;
    let statisticIndicator = [];
    let time = '';
    if (this.selcetedValues1) {
      time = this.startTime;
    } else {
      time = this.commfunc.transDateN(new Date()).substr(0, 7);
    }
    this.sendDataList = []; // 每次查询之前将指标集合置空
    if (this.selectModelList && this.selectModelList.length != 0) {
      this.selectModelList.forEach((el, index) => {
        let valueType_;
        let indicatorValue_;
        if (el[0].value1 && el[0].value2 && el[0].indicatorId && el[0].valueType && el[0].indicatorValue) {
          if (el[0].valueType == '11') {  // 大于等于
            valueType_ = '10';
            indicatorValue_ = el[0].indicatorValue;

          } else if (el[0].valueType == '12') {  // 小于等于
            valueType_ = '10';
            indicatorValue_ = '1,' + el[0].indicatorValue;
          } else {    // 其他取值类型
            valueType_ = el[0].valueType;
            indicatorValue_ = el[0].indicatorValue
          }
          this.sendDataList.push({
            seqId: index + 1 + '',    // 添加条件序号
            indicatorId: el[0].indicatorId,
            valueType: valueType_,
            indicatorValue: indicatorValue_
          })
        }
      });

    }
    this.sendDataList.push({    // 在查询条件中最后添加一条群号的条件
      seqId: this.sendDataList.length + 1 + '',    // 添加条件序号
      indicatorId: this.customGroupId,
      valueType: '02',
      indicatorValue: this.groupId
    })
    if (this.sendDataList && this.sendDataList.length != 0) {
      const tempList = [];
      this.custTargetList.forEach(el => { // 把客户号和客户名称加入临时数组
        tempList.push(el.indicatorId)
      });
      this.showTargetList.forEach(el => { // 把默认显示在表头的指标加入临时数组
        tempList.push(el.indicatorId)
        if (el.showType == '07') {
          statisticIndicator.push(el.indicatorId)
        }
      });
      if (statisticIndicator.length > 0) {
        statisticFlag = true;
      }
      this.downloadList = tempList;
      const params = {
        dataDt: time,  // 数据日期
        conditionList: this.sendDataList,  // 指标列表
        outputFieldList: tempList, // 输出字段列表
        statisticIndicator: statisticIndicator  // 是否汇总
      }
      if (!this.searchContent) {
        params['conditionlogic'] = '01',
          params['conditionlogicDesc'] = '且'
      } else {
        params['conditionlogic'] = '03'
        params['conditionlogicDesc'] = this.searchContent + '且' + params.conditionList.length
      }
      params['pageNum'] = this.searchPageNum;
      params['pageSize'] = this.searchPageSize;
      const configParam = {
        httpService: this.searchHttpService,
        loadingFlag: true,
        messageFlag: false,
        inputParam: params,
        message: this.messageService,
        injectName: 'standardUnifiedQuery',
        loadingShow: this.loadingShow,
        messageInfo: '查询成功',
      }
      this.commonFunc.haddleData(configParam, (bodyData) => {
        if (bodyData) {
          this.searchTableData = bodyData.dataList;
          this.searchTotalNum = bodyData.totalNum;
          if (this.total) {
            if (statisticFlag) {
              bodyData.statistics['sumFlag'] = true;
              this.searchTableData.push(bodyData.statistics)
            } else {
              this.messageService.clear();
              this.messageService.add({ severity: 'error', summary: '提示', detail: `没有可汇总字段` });
            }
          }
        }
      });
    }
  }

  // 分页
  paginate(event) {
    // 每页显示的条数;
    this.searchPageSize = event.rows;
    // 当前页
    this.searchPageNum = event.page + 1;
    this.searchFirst = event.first;
    // 调用查询的方法
    this.doSearch();
  }

  // 查询默认展示的指标
  queryShowTarget(name?) {
    const params = {
      inputParams: { sceneType: '003', status: '1' }
    };
    name ? params['indicatorName'] = name : params['indicatorName'] = '';
    const configParam = {
      httpService: this.searchHttpService,
      loadingFlag: true,
      messageFlag: false,
      inputParam: params,
      message: this.messageService,
      injectName: 'queryIndicatorApplScene',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        bodyData.outputParams.indicatorList.forEach(el => {
          if (el.indicatorName == '客户号') {
            this.customNoId = el.indicatorId;
            this.custTargetList.unshift(el)
          }
          if (el.indicatorName == '客户名称') {
            this.customNameId = el.indicatorId;
            this.custTargetList.push(el)
          }
          if (this.groupFlag == 1 && el.indicatorName == '客户群编号') {
            this.customGroupId = el.indicatorId;
            this.customGroupData.push(el)
          } else if (this.groupFlag == 2 && el.indicatorName == '自选客户群编号') {
            this.customGroupId = el.indicatorId;
            this.customGroupData.push(el)
          }
        });
      }
    });

  }

