  //列表查询`````````````````````````````````````````````````````````````````````````````````````````````
  attrListSearch() {
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    let orgID = commonHeader.orgId;
    let userId = commonHeader.userId;
    let params = {
      listShowsIdType: '2',
      listShowsIdModule: '02',
      createOrg: orgID,
      createdByUserLogin: userId
    }
    this.httpService.selectUserListShow(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.showTargetList = [];
        if (data.exploreConditionObject && data.exploreConditionObject != '') {
          console.log(this.showTargetList);
          let eco = JSON.parse(data.exploreConditionObject);
          console.log(eco);
          eco.forEach(el => {
            this.showTargetList.push(el);
          });
        }
        console.log(this.showTargetList);
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }


  //用户列表登记
  addUserListShow() {
    if (this.selectedHeaderList.length > 0) {
      this.saveArrFlag = true;

    } else {
      this.saveArrFlag = false;
    }
    let params1 = {
      inputParams: {
        sceneType: '003',
        indicatorKindId: this.selectedHeaderList
      }
    };

    const configParam = {   // 查询3级具体指标，并将所有三级指标全部存储到saveIdArr 
      httpService: this.searchHttpService,
      loadingFlag: true,
      messageFlag: false,
      inputParam: params1,
      message: this.messageService,
      injectName: 'queryIndicatorCategory',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.saveIdArr = [];
        if (bodyData.outputParams.indicatorList && bodyData.outputParams.indicatorList.length > 0 && this.saveArrFlag) {
          bodyData.outputParams.indicatorList.forEach(el => {
            if (el.indicatorName != '客户号' && el.indicatorName != '客户名称' && el.indicatorName != '客户群编号') {
              // this.saveIdArr.push({
              //   indicatorId: el.indicatorId,
              // })
              this.saveIdArr.push(el.indicatorId)
            }
          });
        }
        if (this.saveIdArr.length > 50) {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '所选字段不能多于50个' }];
          return false;
        } else {
          this.queryThreeIndicator();
        }
      }
    })
  }

  queryThreeIndicator() {
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    let orgID = commonHeader.orgId;
    let userId = commonHeader.userId;
    let params = {
      inputParams: {
        sceneType: '003',
        indicatorId: this.saveIdArr
      }
    };
    const configParam = {   // 查询3级具体指标，并将所有三级指标全部存储到saveIdArr 
      httpService: this.searchHttpService,
      loadingFlag: true,
      messageFlag: false,
      inputParam: params,
      message: this.messageService,
      injectName: 'queryIndicatorApplScene',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      if (bodyData) {
        this.saveIdHeaderArr = [];
        if (bodyData.outputParams.indicatorList && bodyData.outputParams.indicatorList.length > 0 && this.saveArrFlag) {
          bodyData.outputParams.indicatorList.forEach(el => {
            this.saveIdHeaderArr.push({
              indicatorId: el.indicatorId,
              indicatorName: el.indicatorName,
              indicatorKindId: el.indicatorKindId,
              showType: el.showType
            })
          });
        }
        let params = {
          listShowsIdType: '2',
          listShowsIdModule: '02',
          createOrg: orgID,
          createdByUserLogin: userId,
          exploreConditionObject: JSON.stringify(this.saveIdHeaderArr)
        }
        this.httpService.addUserListShow(params).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.attrListSearch();
          }
          else {
            this.msgs = [];
            data.returnCode.message ? data.returnCode.message : '查询失败！';
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
          }
        }, err => {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
      }
    })
  }

