import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { MessageService } from "primeng/components/common/messageservice";
import { Message } from "primeng/components/common/message";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
    selector: 'app-select-custom-group',
    templateUrl: './select-custom-group.component.html',
    styleUrls: ['./select-custom-group.component.scss'],
    providers: []
})
export class SelectCustomGroupComponent implements OnInit {
    @Input() inValueItem: any;
    @Output() outValueEmit: EventEmitter<any> = new EventEmitter();

    selectCustomGroupForm: FormGroup;  // 查询表单

    tabTable: any = []    // 查询结果

    selectedCar: any = [];  // 列表选中数据集合

    sendData: any = [];  //  发送给查询页面的客户群数据集合

    loadingShow: any = { flag: false }; // loading框

    groupStatus: any = []//群状态下拉框

    pageSize: number = 10;//每页条数
    pageNum: number = 1;//当前页数
    totalNum: number;
    first: number = 0;
    code: any;
    orgId: string;   // 机构ID
    ownerId: string;   // 群主ID
    orgDisplay: boolean = false; // 机构模态框
    personPage: boolean = false;
    tempArr: any = []

    tableMesg: any = tableMessage;





    constructor(
        private messageService: MessageService,
        private httpService: CusOperationHttpService,
        private commonFunc: Customtools,
        private commfunc: Commfunc,
        private fb: FormBuilder,
    ) {
        this.codeValues();
        this.groupStatus = this.code['GroupStatus']
    }
    ngOnInit() {
        this.selectCustomGroupForm = this.fb.group({
            'strategyName': [''],
            'groupId': [''],//群号
            'groupName': [''],//群名称
            'ownerId': [''],//分组所有者ID
            // 'orgName': [''],//所属机构名称
            'orgId': [''],//所属机构ID
            'groupStatus': ['1'],//群组状态
        });
        this.query();
        this.select();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    select() {
        if (this.inValueItem && this.inValueItem != '') {
            this.tempArr = this.inValueItem.split(',')
            this.tempArr.forEach(el => {
                const params = {
                    groupId: el,
                    pageSize: 10,
                    pageNum: 1
                }
                const configParam = {
                    httpService: this.httpService,
                    loadingFlag: false,
                    messageFlag: false,
                    inputParam: params,
                    message: this.messageService,
                    injectName: 'selectCustGroup',
                    loadingShow: this.loadingShow,
                    messageInfo: '查询成功',
                }
                this.commonFunc.haddleData(configParam, (bodyData) => {
                    if (bodyData) {
                        this.selectedCar.push(bodyData.groupList[0])
                    }
                });
            });
        }

    }
    // 点击查询
    doquery() {
        this.first = 0;
        this.pageNum = 1;
        this.query();
    }
    // 重置
    reset() {
        this.selectCustomGroupForm.reset();
    }

    // 分页
    paginate(event) {
        // 每页显示的条数;
        this.pageSize = event.rows;
        // 当前页
        this.pageNum = event.page + 1;
        this.first = event.first;
        // 调用查询的方法
        this.query();
    }

    // 查询客户群
    query() {
        const params = {
            pageSize: this.pageSize,
            pageNum: this.pageNum
        }
        for (var key in this.selectCustomGroupForm.value) {
            if (this.selectCustomGroupForm.value[key]) {
                params[key] = this.selectCustomGroupForm.value[key]
            }
        }
        if (params['orgId']) {
            params['orgId'] = this.orgId;
        }
        if (params['ownerId']) {
            params['ownerId'] = this.ownerId;
        }
        const configParam = {
            httpService: this.httpService,
            loadingFlag: false,
            messageFlag: false,
            inputParam: params,
            message: this.messageService,
            injectName: 'selectCustGroup',
            loadingShow: this.loadingShow,
            messageInfo: '查询成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                console.log(bodyData)
                this.tabTable = bodyData.groupList;
                this.totalNum = bodyData.totalNum;
            }
        });
    }

    selectOrg() {
        // 选择机构
        this.orgDisplay = true;
    }

    selectOwner() {
        this.personPage = true;
    }

    // 选择机构之后的回调函数
    returnOrg(event) {
        if (event.hasOwnProperty('orgName')) {
            this.selectCustomGroupForm.controls['orgId'].setValue(event.orgName);
            this.orgId = event.orgId;
        }
        this.orgDisplay = false;
    }

    // 选择创建人之后的回调函数
    workerCall(event) {
        this.personPage = false;
        if (event.length > 0) {
            this.selectCustomGroupForm.controls['ownerId'].setValue(event[0].tellerName);
            this.ownerId = event[0].tellerId;
        }

    }


        // 清空
        back() {
            this.selectedCar = [];
            this.sendData = [];
        }

    submit() {
        this.selectedCar.forEach(el => {
            if (el.groupName && el.groupId) {
                // debugger
                this.sendData.push({
                    groupName: el.groupName,
                    groupId: el.groupId
                })
            }
        });
        this.outValueEmit.emit(this.sendData);
    }

}