import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { MessageService } from "primeng/components/common/messageservice";
import { Message } from "primeng/components/common/message";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
    selector: 'app-select-assist-store',
    templateUrl: './select-assist-store.component.html',
    styleUrls: ['./select-assist-store.component.scss'],
    providers: []
})
export class SelectAssistStoreComponent implements OnInit {
    @Input() inValueItem: any;
    @Output() outValueEmit: EventEmitter<any> = new EventEmitter()

    selectAssistStoreForm: FormGroup;  // 新增表单

    loadingShow: any = { flag: false }; // loading框

    tabTable: any = []    // 查询结果

    selectedCar: any = [];  // 列表选中的数据集合

    sendData: any = []; // 要发送给查询页面的数据集合

    pageSize: number = 10;
    pageNum: number = 1;
    totalNum: number;
    first: number = 1;
    tempArr: any = [];    // 用来反选

    tableMesg: any = tableMessage;



    constructor(
        private messageService: MessageService,
        private httpService: CustomSearchHttpService,
        private commonFunc: Customtools,
        private fb: FormBuilder,
    ) {

    }
    ngOnInit() {
        this.selectAssistStoreForm = this.fb.group({
            'userId': [''],
            'userName': [''],
            'assistantUserId': [''],
            'orgId': ['']
        });
        this.query();
        this.inValue();

    }


    inValue() {
        if (this.inValueItem && this.inValueItem != '') {
            this.tempArr = this.inValueItem.split(',')
            this.tempArr.forEach(el => {
                const params = {
                    assistantUserId: el,
                    pageSize: 10,
                    pageNum: 1
                }
                const configParam = {
                    httpService: this.httpService,
                    loadingFlag: false,
                    messageFlag: false,
                    inputParam: params,
                    message: this.messageService,
                    injectName: 'queryAssistantUserIdByCondition',
                    loadingShow: this.loadingShow,
                    messageInfo: '查询成功',
                }
                this.commonFunc.haddleData(configParam, (bodyData) => {
                    if (bodyData) {
                        this.selectedCar.push(bodyData.resultList[0])
                    }
                });
            });
        }

    }

    // 点击查询
    doquery() {
        this.first = 0;
        this.pageNum = 1;
        this.query();
    }
    // 重置
    reset() {
        this.selectAssistStoreForm.reset();
    }

    // 分页
    paginate(event) {
        // 每页显示的条数;
        this.pageSize = event.rows;
        // 当前页
        this.pageNum = event.page + 1;
        this.first = event.first;
        // 调用查询的方法
        this.query();
    }

    // 查询协储员
    query() {
        const params = {
            pageSize: this.pageSize,
            pageNum: this.pageNum
        }
        for (var key in this.selectAssistStoreForm.value) {
            if (this.selectAssistStoreForm.value[key]) {
                params[key] = this.selectAssistStoreForm.value[key]
            }
        }
        const configParam = {
            httpService: this.httpService,
            loadingFlag: false,
            messageFlag: false,
            inputParam: params,
            message: this.messageService,
            injectName: 'queryAssistantUserIdByCondition',
            loadingShow: this.loadingShow,
            messageInfo: '查询成功',
        }
        this.commonFunc.haddleData(configParam, (bodyData) => {
            if (bodyData) {
                console.log(bodyData)
                this.tabTable = bodyData.resultList;
                this.totalNum = bodyData.totalNum;
            }
        });
    }

    // 清空
    back() {
        this.selectedCar = [];
        this.sendData = [];
    }

    // 确认
    submit() {
        this.selectedCar.forEach(el => {
            if (el.assistantUserId && el.userName) {
                // debugger
                this.sendData.push({
                    assistantUserId: el.assistantUserId,
                    userName: el.userName
                })
            }
        });
        this.outValueEmit.emit(this.sendData);
    }

}